import { useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useAuth } from "../../../app/auth";
import AuthController from "../../../app/controllers/AuthController";
import Fetch from "../../../app/utils/fetch.service";

export default function EnterNewPasswordAndLogin(props: any) {
    let [searchParams, setSearchParams] = useSearchParams();

    let {
        register,
        handleSubmit,
        formState: { errors, isDirty },
    } = useForm();

    let auth = useAuth();
    let navigate = useNavigate();

    let [responseError, setResponseError] = useState(null) as any;


    function onSubmit(data: any) {
        console.log(data);

        AuthController.setPasswordAndLogin(Object.assign(data, { email: props.email })).then((res: any) => {
            console.log(res);
            if(res?.message) setResponseError(res?.message);

            if (res?.token != null) {
                auth.signin(res, function () {
                    navigate(searchParams.get("login") ?? "/");
                });
            }
        });
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
             {responseError == null ? (
                <></>
            ) : (
                <div className="alert alert-danger fade  alert-dismissible show" role="alert">
                    {responseError}

                    <button type="button" className="btn-close" onClick={() => setResponseError(null)}></button>
                </div>
            )}
            <div className="mb-3">
                <input type="password" placeholder="Enter New Password" className="form-control" {...register("password", { required: true })} />
                {errors?.password && <span className="text-danger text-start fw-bold ">*Password is Required</span>}
            </div>
            <div className="mb-3">
                <input type="password" placeholder="Confirm Password" className="form-control" {...register("password_confirmation", { required: true })} />
                {errors?.password_confirmation && <span className="text-danger text-start fw-bold ">*Confirmation is Required</span>}
            </div>
            <button type="submit" className="btn btn-success my-3 w-100 border-0">
                <i className="fa fa-shield-alt"></i> Login
            </button>
        </form>
    );
}
