import { useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useAuth } from "../../../app/auth";
import AuthController from "../../../app/controllers/AuthController";

export default function Register(props: any) {
    let {
        register,
        handleSubmit,
        formState: { errors, isDirty },
    } = useForm();

    let [searchParams, setSearchParams] = useSearchParams();

    let auth = useAuth();
    let navigate = useNavigate();

    let [responseError, setResponseError] = useState(null) as any;

    function onSubmit(data: any) {
        AuthController.register(data).then((res: any) => {
            if (res?.message != null) {
                setResponseError(res?.message);
                return;
            }

            if (res?.token != null) {
                auth.signin(res, function () {
                    // var myModalEl = document.getElementById("loginModal") as any;
                    // var myModal = new Modal(myModalEl) as any;

                    // myModal.dispose();

                    console.log("hide modal");
                    // props.onLogin();

                    navigate("/accounts");
                });
            }
        });
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)} autoComplete="false">
            <p className="display-6 py-4 text-center">
                <i className="fa fa-user-plus"></i> Register
            </p>

            {responseError == null ? (
                <></>
            ) : (
                <div className="alert alert-danger fade  alert-dismissible show" role="alert">
                    {responseError}

                    <button type="button" className="btn-close" onClick={() => setResponseError(null)}></button>
                </div>
            )}

            <div className="px-2">
                <div className="mb-3">
                    <label className="form-label text-start fw-bold">
                        <i className="fa fa-user"></i> Name
                    </label>
                    <input type="text" {...register("name", { required: true })} placeholder="Enter your Name" className="form-control" />
                    {errors?.name && <span className="text-danger text-start fw-bold ">*Name is Required</span>}
                </div>
                <div className="mb-3">
                    <label className="form-label text-start fw-bold">
                        <i className="fa fa-envelope"></i> Email
                    </label>
                    <input type="email" {...register("email", { required: true })} placeholder="Enter your Email" className="form-control" autoComplete="false" />
                    {errors?.email && <span className="text-danger text-start fw-bold ">*Email is Required</span>}
                </div>
                <div className="mb-3">
                    <label className="form-label text-start fw-bold">
                        <i className="fa fa-phone"></i> Phone Number
                    </label>
                    <input type="phone_number" {...register("phone_number", { required: true })} placeholder="Enter your Phone Number" className="form-control" autoComplete="false" />
                    {errors?.phone_number && <span className="text-danger text-start fw-bold ">*Phone Number is Required</span>}
                </div>
                <div className="mb-3">
                    <div className=" d-flex align-items-center justify-content-between">
                        <label className="form-label  text-start fw-bold text-nowrap">
                            <i className="fa fa-shield-alt"></i> Password
                        </label>
                    </div>
                    <input type="password" {...register("password", { required: true })} placeholder="Enter Password" className="form-control" autoComplete="false" />
                    {errors?.password && <span className="text-danger text-start fw-bold ">*Password is Required</span>}
                </div>
                <div className="mb-3">
                    <label className="col-sm-3 col-form-label text-start fw-bold text-nowrap">
                        <i className="fa fa-shield-alt"></i> Confirm Password
                    </label>
                    <input type="password" {...register("password_confirmation", { required: true })} placeholder="Re-enter Password" className="form-control" />
                    {errors?.password_confirmation && <span className="text-danger text-start fw-bold ">*Confirmation is Required</span>}
                </div>
                <hr />
                <div className="">
                    <button className="btn btn-success my-3 w-100">
                        <i className="fa fa-user-plus"></i> Register
                    </button>
                    <div className="d-flex align-items-center justify-content-center">
                        <p className="text-center m-0">Already registered? </p>
                        <button className="btn btn-sm btn-link py-0" type="submit" onClick={() => props.setIsLogin(true)}>
                            Login
                        </button>
                    </div>
                </div>
            </div>
        </form>
    );
}
