import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle.min.js";

import "leaflet/dist/leaflet.css";
import "./assets/scss/app.scss";

import React from "react";
import { Route, Routes, BrowserRouter, useLocation } from "react-router-dom";
import Home from "./views/pages/home";
import Dashboard from "./views/pages/accounts/dashboard";
import LayoutApp from "./views/pages/layout.app";
import LayoutAccount from "./views/pages/accounts/layout.accounts";
import LayoutHome from "./views/pages/home/layout.home";
import About from "./views/pages/home/about";
import Contact from "./views/pages/home/contact";
import Pricing from "./views/pages/home/pricing";
import Checkout from "./views/pages/checkout";
import Profile from "./views/pages/accounts/profile";
import Orders from "./views/pages/accounts/orders";
import LoginRegister from "./views/pages/login-register";
import { AuthMiddleware } from "./app/auth";
import Settings from "./views/pages/accounts/settings";
import ViewOrder from "./views/pages/accounts/orders/view";
import Recharges from "./views/pages/accounts/recharges";
import Purchases from "./views/pages/accounts/purchases";
import TrackDelivery from "./views/pages/delivery/track";
import Gallery from "./views/pages/home/gallery";

function App() {
    let location = useLocation() as any;
    // const location = useLocation();
    // const background = location.state || location.state?.background;

    // console.log(background);
    // console.log(location);

    return (
        // <BrowserRouter>
        <>
            <Routes>
                {/* <Routes location={background || location}> */}
                <Route path="/" element={<LayoutApp />}>
                    <Route path="/delivery/:id/" element={<TrackDelivery />} />
                    <Route element={<LayoutHome />}>
                        <Route index element={<Home />}></Route>
                        <Route path="about" element={<About />}></Route>
                        <Route path="contact" element={<Contact />}></Route>
                        <Route path="gallery" element={<Gallery />}></Route>
                        <Route path="pricing" element={<Pricing />}></Route>
                    </Route>
                    <Route
                        path="accounts"
                        element={
                            <AuthMiddleware>
                                <LayoutAccount />
                            </AuthMiddleware>
                        }
                    >
                        <Route path="dashboard" element={<Dashboard />}></Route>
                        <Route path="profile" element={<Profile />}></Route>
                        {/* <Route path="recharges" element={<Recharges />}></Route> */}
                        <Route path="purchases" element={<Purchases />}></Route>
                        <Route path="orders">
                            <Route index element={<Orders />}></Route>
                            <Route path=":id" element={<ViewOrder />}></Route>
                        </Route>
                        <Route path="settings" element={<Settings />}></Route>
                    </Route>
                    <Route
                        path="checkout"
                        element={
                            <AuthMiddleware>
                                <Checkout />
                            </AuthMiddleware>
                        }
                    ></Route>
                </Route>
            </Routes>
            {/* {background && <Routes><Route element={<LoginRegister />} /></Routes>} */}
        </>
        // </BrowserRouter>
    );
}

export default App;
