import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../../../../app/auth";
import OrderController from "../../../../app/controllers/OrderController";
import Spinner from "../../../components/common/spinner";

export default function Orders() {
    let [orders, setOrders] = useState(null) as any;

    let auth = useAuth();
    useEffect(() => {
        OrderController.index().then((res: any) => {
            console.log(res);
            if (res?.message != null) return;
            setOrders(res);
        });
    }, [auth.session.company]);

    return (
        <div className="text-dark">
            <div className="d-flex align-items-center justify-content-between mb-2">
                <p className="h5 m-0">
                    <i className="fa fa-file-invoice"></i> Orders
                </p>
            </div>
            <hr />

            <div className="list-group">
                {orders == null ? (
                    <Spinner />
                ) : (
                    orders?.map((order: any) => (
                        <Link to={"" + order.id} key={order.id} className="list-group-item list-group-item-action">
                            <div className="h6 d-flex align-items-center justify-content-between mb-2">
                                <div className="">
                                    <i className={`bg-light rounded-circle p-1 fa ${order.status.icon} text-${order.status.color}`}></i>
                                    Order #{order.id}
                                    {/* <small className="fw-light ">{{ $order->created_at }}</small> */}
                                </div>

                                {/* <div className="d-flex align-items-center">
                                    <span className=""> {{ number_format($order->total, 2) }}</span>
                                    <a href="{{ route('digital.accounts.orders', ['order' => $order]) }}" className="ms-2 btn btn-sm btn-dark">View</a>
                                </div> */}
                            </div>
                        </Link>
                    ))
                )}
            </div>
        </div>
    );
}
