import { authCompany, authToken } from "../auth";
import Fetch from "../utils/fetch.service";

export default class OrderController {
    static async index() {
        const response = await Fetch.get("/" + authCompany()?.domain + "/orders", { token: authToken() });
        return response;
    }

    static async show(order_id: any) {
        // console.log(authCompany());
        // querystring.stringify(query);
        const response = await Fetch.get("/" + authCompany()?.domain + "/orders/" + order_id, { token: authToken() });
        return response;
    }

    static async store(data: any) {
        console.log(authCompany());
        // querystring.stringify(query);
        const response = await Fetch.post("/" + authCompany()?.domain + "/orders", { token: authToken(), body: data });
        return response;
    }

    static async pay(order_id: any) {
        const response = await Fetch.post("/" + authCompany()?.domain + "/orders/" + order_id + "/pay", { token: authToken() });
        return response;
    }
}
