import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../../../../app/auth";
import PurchaseController from "../../../../app/controllers/PurchaseController";
import RechargeController from "../../../../app/controllers/RechargeController";
import Spinner from "../../../components/common/spinner";
import logo from "./../../../../assets/images/logo.png";

export default function Dashboard() {
    let auth = useAuth();

    let [recharges, setRecharges] = useState(null) as any;

    useEffect(() => {
        RechargeController.index({ scope: "active" }).then((res: any) => {
            // console.log(res);
            if (res?.message != null) return;   
            setRecharges(res);
        });
    }, [auth.session.company]);

    return (
        <>
            <p className="h5 text-dark">
                <i className="fa fa-tachometer-alt"></i> Dashboard
            </p>
            <hr />
            <div className="d-flex justify-content-between text-dark">
                <p className="h3 fw-normal">Your Active Purchase</p>
                <div className="">
                    <a href={window.location.protocol + "//" + auth.session.company.domain + "." + process.env.REACT_APP_HOST_NAME + "/login?email=" + auth.session.user.email} target="blank" className="d-flex align-items-center btn btn-dark btn-sm rounded-pill fw-bold ps-3">
                        <i className="fa fa-external-link me-2"></i> Go to <img className="ms-2 bg-light p-1 rounded-pill px-2" src={logo} height="20px" alt="" />
                    </a>
                </div>
            </div>

            {recharges == null ? (
                <Spinner />
            ) : (
                <ul className="list-group">
                    {recharges?.length == 0 ? (
                        <li className="list-group-item py-4">
                            <p className="mb-2 text-center h3 fw-light">No Active Puchase</p>
                            <div className="text-center">
                                <Link to={"/checkout"} className="btn btn-sm btn-secondary">
                                    <i className="fa fa-shopping-cart"></i> Order Now
                                </Link>
                            </div>
                        </li>
                    ) : (
                        recharges.map((recharge: any) => (
                            <li key={recharge.id} className="list-group-item h6 fw-normal">
                                <div className="d-flex align-items-start justify-content-between">
                                    <div className="">
                                        <p className="mb-1">
                                            <span className="fw-bold">Start DateTime:</span> {recharge.start_date.date}
                                        </p>
                                        <p className="mb-1">
                                            <span className="fw-bold">End DateTime:</span> {recharge.end_date.date}
                                        </p>
                                        <div className="small">
                                            {recharge.purchase.number_of_devices > 0 ? (
                                                <div className="badge bg-success me-2">
                                                    <i className="fa fa-microchip"></i> Devices <span className="small rounded px-1 bg-light text-dark fw-bold">25</span>
                                                </div>
                                            ) : (
                                                <></>
                                            )}

                                            {recharge.purchase.number_of_licenses > 0 ? (
                                                <div className="badge bg-success me-2">
                                                    <i className="fa fa-file-certificate"></i> Licenses <span className="small rounded px-1 bg-light text-dark fw-bold">25</span>
                                                </div>
                                            ) : (
                                                <></>
                                            )}
                                        </div>
                                    </div>
                                    <div className="text-end">
                                        <div className="">
                                            <div className="badge bg-dark p-3">
                                                <span className="text-capitalize">{recharge?.days_left} Days Left</span>
                                            </div>
                                        </div>
                                        <Link className="small" to={"/accounts/orders/" + recharge?.order_id}>
                                            View Order
                                        </Link>
                                    </div>
                                </div>
                            </li>
                        ))
                    )}
                </ul>
            )}
        </>
    );
}
