import { useEffect, useState } from "react";
import { Link, NavLink, Outlet, useLocation, useNavigate } from "react-router-dom";
import { useAuth } from "../../../app/auth";
import OrganizationForm from "./organization/form";

export default function LayoutAccount() {
    let auth = useAuth();

    console.log(auth.session.user);

    let [selectedOrganization, setSelectedOrganization] = useState(auth.session.company) as any;

    function organizationChanged(value: any) {
        // if()
        if (value == "") setSelectedOrganization(null);

        // if
        // console.log(auth.session.user.companies.filter((cn: any) => cn.id == value)[0]);
        auth.switchCompany(auth.session.user.companies.filter((cn: any) => cn.id == value)[0]);
        setSelectedOrganization(auth.session.user.companies.filter((cn: any) => cn.id == value)[0]);
    }

    let navigate = useNavigate();
    let location = useLocation();

    useEffect(() => {
        if (selectedOrganization == null) {
            navigate("/accounts");
        } else {
            if (location.pathname == "/accounts") navigate("/accounts/dashboard");
        }
        console.log(selectedOrganization);
    }, [selectedOrganization]);

    useEffect(() => {
        setSelectedOrganization(auth.session.company);
    }, [auth.session.company]);

    function companySaved(company: any) {
        // console.log(company);

        auth.addCompany(company);
        // let companies = auth.session.user.companies as any[];

        // companies.push(company);

        // console.log(companies);
        // auth.add;

        // console.log(auth.session.user);
    }

    return (
        <main>
            <div className="p-3">
                <div className="row">
                    <div className="col-md-3 border-end" style={{ minHeight: "80vh" }}>
                        <div className="mb-3">
                            <select value={selectedOrganization?.id ?? ""} onChange={(e) => organizationChanged(e.target.value)} className="form-select" name="" id="">
                                <option value={""}>--- New Organization ---</option>
                                {auth.session.user.companies?.map((company: any) => (
                                    <option key={company.id} value={company.id}>
                                        {" "}
                                        {company.domain}.{process.env.REACT_APP_HOST_NAME}
                                    </option>
                                ))}
                            </select>
                        </div>
                        {/* <div className="mb-3 rounded p-2 text-center text-dark" style={{ background: "#eef3ef" }}>
                            <div className="mb-2">
                                <p className="m-0 fw-bold p-1 rounded mb-1" style={{ background: "rgba(0,0,0,.05)" }}>
                                    <i className="fa fa-building"></i> Company
                                </p>
                                <p className="m-0 h6 fw-normal">{"Essenrfid"}</p>
                            </div>
                            <div className="mb-2">
                                <p className="m-0 fw-bold p-1 rounded mb-1" style={{ background: "rgba(0,0,0,.05)" }}>
                                    <i className="fa fa-user-shield"></i> Admin
                                </p>
                                <p className="m-0 h6 fw-normal">{"Danish Shaikh"}</p>
                            </div>
                        </div> */}
                        <nav className="nav nav-pills flex-column">
                            {selectedOrganization == null ? (
                                <>
                                    <p className="text-center text-danger fw-bold">No Organization Selected</p>
                                </>
                            ) : (
                                <>
                                    <div className="text-dark fw-bold mb-2">Personal Details</div>
                                    <NavLink to={"/accounts/profile"} className="h6 px-3 py-2 nav-link">
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="">
                                                <i style={{ width: 20 }} className="text-center fa fa-id-card-alt"></i> Profile
                                            </div>
                                            <div className="badge bg-light text-dark"></div>
                                        </div>
                                    </NavLink>
                                    <div className="text-dark fw-bold mb-2">Organization Details</div>
                                    <NavLink to={"/accounts/dashboard"} className="h6 px-3 py-2 nav-link  ">
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="">
                                                <i style={{ width: 20 }} className="text-center fa fa-tachometer"></i> Dashboard
                                            </div>
                                            <div className="badge bg-light text-dark"></div>
                                        </div>
                                    </NavLink>
                                    <NavLink to={"/accounts/orders"} className="h6 px-3 py-2 nav-link">
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="">
                                                <i style={{ width: 20 }} className="text-center fa fa-file-invoice"></i> Orders
                                            </div>
                                            {/* <div className="badge bg-light text-dark">{0}</div> */}
                                        </div>
                                    </NavLink>
                                    <NavLink to={"/accounts/purchases"} className="h6 px-3 py-2 nav-link">
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="">
                                                <i style={{ width: 20 }} className="text-center fa fa-file-invoice"></i> Purchases
                                            </div>
                                            {/* <div className="badge bg-light text-dark">{0}</div> */}
                                        </div>
                                    </NavLink>
                                    {/* <NavLink to={"/accounts/recharges"} className="h6 px-3 py-2 nav-link">
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="">
                                                <i style={{ width: 20 }} className="text-center fa fa-file-invoice"></i> Recharges
                                            </div>
                                        </div>
                                    </NavLink> */}
                                    <NavLink to={"/accounts/settings"} className="h6 px-3 py-2 nav-link">
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="">
                                                <i style={{ width: 20 }} className="text-center fa fa-cogs"></i> Settings
                                            </div>
                                        </div>
                                    </NavLink>
                                </>
                            )}
                            {/* <a className="h6 px-3 py-2 nav-link {{ Route::currentRouteName() == 'digital.accounts.security' ? 'active' : '' }}" href="{{ route('digital.accounts.security') }}">
                                <div className="d-flex align-items-center justify-content-between">
                                    <div className="">
                                        <i style={{ width: 20 }} className="text-center fa fa-shield-alt"></i>
                                        Security
                                    </div>
                                    <div className="badge bg-light text-dark"></div>
                                </div>
                            </a> */}
                            {/* <a className="h6 px-3 py-2 nav-link {{ Route::currentRouteName() == 'digital.accounts.inbox' ? 'active' : '' }}" href="{{ route('digital.accounts.inbox') }}">
                                <div className="d-flex align-items-center justify-content-between">
                                    <div className="">
                                        <i style={{ width: 20 }} className="text-center fa fa-inbox"></i>
                                        Inbox
                                    </div>
                                    <div className="badge bg-light text-dark"></div>
                                </div>
                            </a> */}
                        </nav>
                    </div>
                    <div className="col-md-9">{selectedOrganization == null ? <OrganizationForm onSaved={(company: any) => companySaved(company)} selectedOrganization={selectedOrganization} /> : <Outlet />}</div>
                </div>
            </div>
        </main>
    );
}
