import { Outlet } from "react-router-dom";
import xtenna from "./../../assets/images/xtenna.svg";
import rfid from "./../../assets/images/rfid.svg";
import Navbar from "../components/Navbar";

export default function LayoutApp() {
    return (
        <div className="app">
            <div className="container-xxl">
                <Navbar />

                <Outlet />
                <div className="row d-flex align-items-center justify-content-between py-3 flex-wrap">
                    <div className="text-center col-md-4">
                        <img src={xtenna} style={{ filter: "contrast(10) invert(1) drop-shadow(2px 4px 6px black)" }} height="50px" alt="" className="" />
                    </div>
                    <div className="text-center col-md-4">
                        <a target="blank" href=" https://www.essenrfid.com" className="text-light fw-light h5">
                            www.essenrfid.com
                        </a>
                    </div>
                    <div className="text-center col-md-4">
                        <img src={rfid} style={{ filter: "contrast(10) invert(1) drop-shadow(2px 4px 6px black)" }} height="50px" alt="" className="" />
                    </div>
                </div>
            </div>
        </div>
    );
}
