import { useAuth } from "../../../../app/auth";
import OrganizationForm from "../organization/form";

export default function Settings() {
    let auth = useAuth();

    function updateCompany(company: any) {
        // console.log(company);
        auth.addCompany(company);

    }

    return <OrganizationForm onSaved={(company: any) => updateCompany(company)} selectedOrganization={auth.session.company} />;
}
