import React, { useContext, useState } from "react";
import { Navigate, useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useCookies, Cookies } from "react-cookie";

interface UserType {
    id: string;
    name: String;
    email: String;
    companies: any;
    role: any;
    active: any;
    email_verified_at: any;
}

interface AuthType {
    user: UserType;
    company: any;
    token: string;
}

interface AuthContextType {
    session: AuthType;
    signin: (session: AuthType, callback: VoidFunction) => void;
    signout: (callback: VoidFunction) => void;
    addCompany: (company: any) => void;
    updateProfile: (user: any) => void;
    switchCompany: (company: any) => void;
    // updateUser: (user: UserType, callback: VoidFunction) => void;
}

let AuthContext = React.createContext<AuthContextType>(null!);

export function authToken() {
    // return new Cookies().get("session")?.token;
    return JSON.parse(localStorage.getItem("session") ?? "{}")?.token;
}
export function authCompany() {
    // return new Cookies().get("session")?.company;
    return JSON.parse(localStorage.getItem("session") ?? "{}")?.company;
}

// Provider component that wraps your app and makes auth object ...
// ... available to any child component that calls useAuth().
export function AuthProvider({ children }: any) {
    // const auth = useProvideAuth();

    // const [cookies, setCookie, removeCookie] = useCookies(["session"]);

    // let session = {
    //     user: {
    //         id: "1",
    //         name: "Danish",
    //         email: "daa",
    //     },
    //     token: "dfds",
    // } as AuthType;

    const session = JSON.parse(localStorage.getItem("session") ?? "{}");

    let signin = (session: any, callback: VoidFunction) => {
        // console.log(cookies);
        // removeCookie("session");
        localStorage.removeItem("session");

        // setCookie("session", JSON.stringify(Object.assign(session, { company: session.user.companies[0] })));
        localStorage.setItem("session", JSON.stringify(Object.assign(session, { company: session.user.companies[0] })));
        auth.session = JSON.parse(localStorage.getItem("session") ?? "{}");

        callback();
    };
    let signout = (callback: VoidFunction) => {
        // removeCookie("session");
        localStorage.removeItem("session");
        // cookies.session = null;
        auth.session = JSON.parse(localStorage.getItem("session") ?? "{}");

        console.log("logout");
        callback();
    };

    let addCompany = (company: any) => {
        let companies = auth.session.user.companies as any[];

        if (companies.some((companyN: any) => companyN.id == company.id)) {
            companies = companies.map((companyN: any) => (companyN.id == company.id ? company : companyN));
        } else {
            companies.push(company);
        }

        // if (companies.indexOf(company) === -1) {
        // } else {
        // }

        let session: AuthType = {
            company: company,
            token: auth.session.token,
            user: Object.assign(auth.session.user, { companies: companies }),
        } as AuthType;

        // console.log(session);

        // removeCookie("session");
        localStorage.removeItem("session");
        // setCookie("session", JSON.stringify(session));
        localStorage.setItem("session", JSON.stringify(session));
        auth.session = JSON.parse(localStorage.getItem("session") ?? "{}");
    };

    let updateProfile = (user: any) => {
        let session: AuthType = {
            company: auth.session.company,
            token: auth.session.token,
            user: user,
        } as AuthType;

        // removeCookie("session");
        localStorage.removeItem("session");

        // setCookie("session", JSON.stringify(session));
        localStorage.setItem("session", JSON.stringify(session));
    };

    let switchCompany = (company: any) => {
        let session: AuthType = {
            company: company,
            token: auth.session.token,
            user: auth.session.user,
        } as AuthType;

        localStorage.removeItem("session");

        // removeCookie("session");
        localStorage.setItem("session", JSON.stringify(session));
        // setCookie("session", JSON.stringify(session));
    };

    let auth = { session: session, signin, signout, addCompany, updateProfile, switchCompany };

    return <AuthContext.Provider value={auth}>{children}</AuthContext.Provider>;
}

// Hook for child components to get the auth object ...
// ... and re-render when it changes.
export const useAuth = () => {
    // const [cookies, setCookie, removeCookie] = useCookies(["session"]);

    // console.log(cookies);

    // return useContext(authContext);
    return React.useContext(AuthContext);
};

export function AuthMiddleware({ children }: { children: JSX.Element }) {
    let auth = useAuth();
    let location = useLocation();
    let navigate = useNavigate();

    let [searchParams, setSearchParams] = useSearchParams();

    console.log(auth);

    if (!auth?.session?.user) {
        // Redirect them to the /login page, but save the current location they were
        // trying to go to when they were redirected. This allows us to send them
        // along to that page after they login, which is a nicer user experience
        // than dropping them off on the home page.
        // console.log(history.goBack());
        console.log(location);
        // return <Navigate to="/login" state={{ from: location }} replace />;
        return <Navigate to={"/?login=" + location.pathname} state={{ from: location }} replace />;
        // return <></>;
    }

    return children;
}

export function GuestMiddleware({ children }: { children: JSX.Element }) {
    let auth = useAuth();
    let location = useLocation();

    console.log(auth);

    if (auth.session.user) {
        // Redirect them to the /login page, but save the current location they were
        // trying to go to when they were redirected. This allows us to send them
        // along to that page after they login, which is a nicer user experience
        // than dropping them off on the home page.
        return <Navigate to="/" state={{ from: location }} replace />;
    }

    return children;
}
