import video from "./../../../assets/video.mp4";
import cod from "./../../../assets/images/cod.svg";
import ot from "./../../../assets/images/ot.svg";
import bp from "./../../../assets/images/bp.svg";
import videoBanner from "./../../../assets/images/video.png";
import screen from "./../../../assets/images/screen.png";
import strada from "./../../../assets/images/strada.png";
import plan_business from "./../../../assets/images/plan_business.jpeg";
import plan_enterprise from "./../../../assets/images/plan_enterprise.jpeg";
import plan_startup from "./../../../assets/images/plan_startup.jpeg";

import { Link } from "react-router-dom";

export default function Home() {
    return (
        <div className="px-2">
            <div className="row">
                <div className="col-lg-5">
                    <video controls src={video} controlsList="nodownload" poster={videoBanner} className="shadow mt-3 rounded border border-dark border-2" autoPlay={false} width="100%" />

                    <div className="row text-center gx-0 mb-3">
                        <div className="col-sm-4">
                            <div className="card-body link">
                                <div className="rounded-circle mx-auto d-flex align-items-center justify-content-center mt-2">
                                    <div className="">
                                        <img src={cod} alt="" width="30px" />
                                    </div>
                                </div>
                                <p className="fw-bold">Reduce Cost of Delivery</p>
                                <p className="m-0 small">On time delivery &amp; material safety with more delivery handling per vehicle increases efficiency.</p>
                            </div>
                        </div>
                        <div className="col-sm-4">
                            <div className="card-body link">
                                <div className="rounded-circle mx-auto d-flex align-items-center justify-content-center mt-2">
                                    <div className="">
                                        <img src={ot} alt="" width="30px" />
                                    </div>
                                </div>
                                <p className="fw-bold">On Tracking</p>
                                <p className="m-0 small">Continuous monitoring helps in reducing inefficiency &amp; enhancing security</p>
                            </div>
                        </div>
                        <div className="col-sm-4">
                            <div className="card-body link">
                                <div className="rounded-circle mx-auto d-flex align-items-center justify-content-center mt-2">
                                    <div className="">
                                        <img src={bp} alt="" width="30px" />
                                    </div>
                                </div>
                                <p className="fw-bold">Better Planning</p>
                                <p className="m-0 small">Better route optimization &amp; customer notification makes delivery easier for customer &amp; increasing company profitability having better control of operations.</p>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-lg-7">
                    <div className="row py-3 gx-0">
                        <div className="col-md-4 ">
                            <Link to={"/gallery"} className="link d-flex justify-content-end px-2 py-2">
                                <div className="">
                                    <p className="h5 fw-normal text-end text-dark">Dashboards offered</p>
                                    <div className="text-end">
                                        <span className="h6 " style={{ color: "#FF9101", borderBottom: "2px solid" }}>
                                            <i className="fas fa-photo-video"></i> Show All Screens
                                        </span>
                                    </div>
                                </div>
                                <i className="fad fa-play flex-shrink-0 h3 ms-1" style={{ color: "#A7ABB7" }}></i>
                            </Link>

                            <div className="mt-3 pe-1" style={{ borderRight: "2px solid #FF9101" }}>
                                <p className="text-end h5 fw-normal">Unique Features</p>

                                <ul className="small">
                                    <li>Notifications to customer for the upcoming delivery schedule.</li>
                                    <li>On time delivery &amp; visibility, results reduced anxiety in wait time of customer.</li>
                                    <li>Optimized delivery orders using AI assisted optimized route schedule.</li>
                                    <li>Business analytics for greater Management visibility. </li>
                                </ul>
                            </div>
                        </div>

                        <div className="col-md-8 ps-3">
                            <div id="indexCarousel" className="carousel slide" data-bs-ride="carousel">
                                <ol className="carousel-indicators">
                                    <li data-bs-target="#indexCarousel" data-bs-slide-to="0" className="active"></li>
                                    <li data-bs-target="#indexCarousel" data-bs-slide-to="1"></li>
                                    <li data-bs-target="#indexCarousel" data-bs-slide-to="2"></li>
                                </ol>
                                <div className="carousel-inner rounded" role="listbox">
                                    <div className="carousel-item active">
                                        <img src={screen} width="100%" alt="" />
                                    </div>
                                    <div className="carousel-item">
                                        <img src={screen} width="100%" alt="" />
                                    </div>
                                    <div className="carousel-item">
                                        <img src={screen} width="100%" alt="" />
                                    </div>
                                </div>
                                <button className="carousel-control-prev" type="button" data-bs-target="#indexCarousel" data-bs-slide="prev">
                                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                                    <span className="visually-hidden">Previous</span>
                                </button>
                                <button className="carousel-control-next" type="button" data-bs-target="#indexCarousel" data-bs-slide="next">
                                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                                    <span className="visually-hidden">Next</span>
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-8">
                            <div className="link p-3 mb-3">
                                <div className="row gx-0">
                                    <div className="col-md-8">
                                        <div className=" mb-2">
                                            <p className="h4 fw-normal">STRADA PLUS</p>
                                            <p className="small fw-bold m-0">Advantages of Strada Plus</p>
                                            <p className="m-0 small">Single integrated unit, easily mountable Eliminates need for regular on-site power source Simple plug-and-play device Overcomes RF limitations</p>
                                        </div>
                                        <div className="link p-2">
                                            <div className="d-flex align-items-center">
                                                <i className="fa fa-file-pdf h3 m-0 me-3 text-danger"></i>
                                                <div className="">
                                                    <p className="m-0 fw-bold">Brochure</p>
                                                    <p className="m-0">Download</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="col-md-4 text-center">
                                        <img src={strada} className="rounded" height="150px" alt="" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="text-end">
                                <p className="m-0 h5 fw-normal">
                                    Delivery insight for <br />
                                    your logistics team.
                                </p>
                                <p className="m-0 ">
                                    Improve delivery experience <br />
                                    of your customers.
                                </p>

                                <div className="d-flex justify-content-end mt-2">
                                    <div className="link ms-1 p-1 d-flex align-items-center">
                                        <img className="rounded p-1" height="40px" src={plan_enterprise} alt="" />
                                        <span className="add-on text-nowrap ms-1">
                                            <span className="fw-bold text-dark text-nowrap">Enterprise Plan</span>
                                            <div className="">
                                                <Link to={'/pricing'} className="m-0 small">
                                                    View More
                                                </Link>
                                            </div>
                                        </span>
                                    </div>

                                    <div className="link  ms-1 p-1 d-flex align-items-center">
                                        <img className="rounded p-1" height="40px" src={plan_business} alt="" />
                                        <span className="add-on text-nowrap ms-1">
                                            <span className="fw-bold text-dark text-nowrap">Business Plan</span>
                                            <div className="">
                                                <Link to={'/pricing'} className="m-0 small">
                                                    View More
                                                </Link>
                                            </div>
                                        </span>
                                    </div>
                                </div>
                                <div className=" mt-2 mb-2">
                                    <Link to={"/pricing"} style={{ background: "linear-gradient(45deg, #db6800, #b85b00)", textShadow: "0 0 2px", border: 0, boxShadow: "5px 5px 15px rgb(0 0 0 / 50%)" }} className="btn btn-secondary py-2 px-5">
                                        <i className="fa fa-shopping-cart"></i> Order Now
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
