import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../../../../app/auth";
import PurchaseController from "../../../../app/controllers/PurchaseController";
import Spinner from "../../../components/common/spinner";

export default function Purchases() {
    let [purchases, setPurchases] = useState(null) as any;

    let auth = useAuth();
    useEffect(() => {
        PurchaseController.index().then((res: any) => {
            console.log(res);
            if (res?.message != null) return;
            setPurchases(res);
        });
    }, [auth.session.company]);

    return (
        <div className="text-dark">
            <div className="d-flex align-items-center justify-content-between mb-2">
                <p className="h5 m-0">
                    <i className="fa fa-file-invoice"></i> Subscription Purchases
                </p>
            </div>
            <hr />

            <div className="list-group">
                {purchases == null ? (
                    <Spinner />
                ) : purchases?.length == 0 ? (
                    <li className="list-group-item py-4">
                        <p className="mb-2 text-center h3 fw-light">No Puchase</p>
                        <div className="text-center">
                            <Link to={"/checkout"} className="btn btn-sm btn-secondary">
                                <i className="fa fa-shopping-cart"></i> Order Now
                            </Link>
                        </div>
                    </li>
                ) : (
                    purchases?.map((purchase: any) => (
                        <li key={purchase.id} className="list-group-item h6 fw-normal">
                            <div className="d-flex align-items-start justify-content-between">
                                <div className="">
                                    <p className="h3">Purchase #{purchase.id ?? 1}</p>
                                    {/* <p className="mb-1">
                                        <span className="fw-bold">Start DateTime:</span> {recharge.start_date.date}
                                    </p>
                                    <p className="mb-1">
                                        <span className="fw-bold">End DateTime:</span> {recharge.end_date.date}
                                    </p> */}
                                    <div className="small">
                                        {purchase.number_of_devices > 0 ? (
                                            <div className="badge bg-success me-2">
                                                <i className="fa fa-microchip"></i> Devices <span className="small rounded px-1 bg-light text-dark fw-bold">25</span>
                                            </div>
                                        ) : (
                                            <></>
                                        )}

                                        {purchase.number_of_licenses > 0 ? (
                                            <div className="badge bg-success me-2">
                                                <i className="fa fa-file-certificate"></i> Licenses <span className="small rounded px-1 bg-light text-dark fw-bold">25</span>
                                            </div>
                                        ) : (
                                            <></>
                                        )}
                                    </div>
                                </div>
                                <div className="text-end">
                                    <div className="">
                                        {purchase?.active_recharge?.days_left > 0 ? (
                                            <div className="badge bg-dark p-3">
                                                <span className="text-capitalize">{purchase?.active_recharge?.days_left} Days Left</span>
                                            </div>
                                        ) : (
                                            <>
                                                <button className="btn btn-outline-dark me-2">
                                                    <span className="text-capitalize">
                                                        <i className="fa fa-shopping-cart"></i> Recharge Now
                                                    </span>
                                                </button>
                                                <div className="badge bg-danger p-3">
                                                    <span className="text-capitalize">
                                                        <i className="fa fa-globe"></i> Expired
                                                    </span>
                                                </div>
                                            </>
                                        )}
                                    </div>
                                    {/* <Link className="small" to={"/accounts/orders/" + recharge?.order?.id}>
                                        View Order
                                    </Link> */}
                                </div>
                            </div>
                        </li>
                    ))
                )}
            </div>
        </div>
    );
}
