import { useEffect, useState } from "react";

export default function BillingAddress(props: any) {
    let [isSame, setIsSame] = useState(false);

    let billingAddress = props?.billingAddress;
    let shippingAddress = props?.shippingAddress;

    let [edit, setEdit] = useState(false);

    let [billing_address_line1, set_billing_address_line1] = useState("") as any;
    let [billing_address_line2, set_billing_address_line2] = useState("") as any;
    let [billing_address_city, set_billing_address_city] = useState("") as any;
    let [billing_address_state, set_billing_address_state] = useState("") as any;
    let [billing_address_zipcode, set_billing_address_zipcode] = useState("") as any;
    let [billing_address_country, set_billing_address_country] = useState("") as any;

    useEffect(() => {
        if (!isSame) {
            set_billing_address_line1("");
            set_billing_address_line2("");
            set_billing_address_city("");
            set_billing_address_state("");
            set_billing_address_zipcode("");
            set_billing_address_country("");
            props.onSaved(null);
            setEdit(false);
        } else {
            set_billing_address_line1(shippingAddress?.shipping_address_line1);
            set_billing_address_line2(shippingAddress?.shipping_address_line2);
            set_billing_address_city(shippingAddress?.shipping_address_city);
            set_billing_address_state(shippingAddress?.shipping_address_state);
            set_billing_address_zipcode(shippingAddress?.shipping_address_zipcode);
            set_billing_address_country(shippingAddress?.shipping_address_country);
            props.onSaved({
                billing_address_line1: shippingAddress?.shipping_address_line1,
                billing_address_line2: shippingAddress?.shipping_address_line2,
                billing_address_city: shippingAddress?.shipping_address_city,
                billing_address_state: shippingAddress?.shipping_address_state,
                billing_address_zipcode: shippingAddress?.shipping_address_zipcode,
                billing_address_country: shippingAddress?.shipping_address_country,
            });
            setEdit(false);
        }
    }, [isSame, shippingAddress]);

    return (
        <div className="p-3 rounded mb-3" style={{ background: "#EEF3EF" }}>
            <div className="d-flex bg-dark text-light px-3 py-2 rounded align-items-center justify-content-between mb-2">
                <p className="h6 m-0">{billingAddress != null ? <i className="bg-light rounded-circle p-1 fa fa-check-circle text-success"></i> : <i className="bg-light rounded-circle p-1 fa fa-exclamation-circle text-warning"></i>} Billing Address</p>

                {edit ? (
                    <div>
                        <button
                            disabled={billing_address_line1 == "" || billing_address_city == "" || billing_address_state == "" || billing_address_zipcode == "" || billing_address_country == ""}
                            onClick={() => {
                                props.onSaved({
                                    billing_address_line1: billing_address_line1,
                                    billing_address_line2: billing_address_line2,
                                    billing_address_city: billing_address_city,
                                    billing_address_state: billing_address_state,
                                    billing_address_zipcode: billing_address_zipcode,
                                    billing_address_country: billing_address_country,
                                });
                                setIsSame(false);
                                setEdit(false);
                            }}
                            className="click  btn btn-success btn-sm p-0 px-1 me-1"
                        >
                            <i className="fa fa-save"></i> Save
                        </button>
                        <div onClick={() => setEdit(false)} className="click  btn btn-danger btn-sm p-0 px-1">
                            <i className="fa fa-times"></i>
                        </div>
                    </div>
                ) : isSame ? (
                    <></>
                ) : (
                    <div onClick={() => setEdit(true)} className="click  btn btn-light btn-sm p-0 px-1">
                        <i className="fa fa-edit"></i>
                    </div>
                )}
            </div>
            <div className="px-2">
                <div className="row h6">
                    <div className="col-md-12">
                        <label className="p-2">
                            <input
                                type="checkbox"
                                checked={isSame}
                                onChange={(e) => {
                                    // console.log(shippingAddress);
                                    setIsSame(e.target.checked);
                                }}
                            />{" "}
                            Same as shipping address
                        </label>
                    </div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Line 1 </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input value={billing_address_line1} onChange={(e) => set_billing_address_line1(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{billing_address_line1 ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Line 2 </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input value={billing_address_line2} onChange={(e) => set_billing_address_line2(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{billing_address_line2 ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">City </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input value={billing_address_city} onChange={(e) => set_billing_address_city(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{billing_address_city ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">State </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input value={billing_address_state} onChange={(e) => set_billing_address_state(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{billing_address_state ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Pincode </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input value={billing_address_zipcode} onChange={(e) => set_billing_address_zipcode(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{billing_address_zipcode ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Country </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input value={billing_address_country} onChange={(e) => set_billing_address_country(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{billing_address_country ?? ""}</p>}</div>
                </div>
            </div>
        </div>
    );
}
