import { useState } from "react";
import { useAuth } from "../../../app/auth";

export default function ShippingAddress(props: any) {
    let shippingAddress = props?.shippingAddress;

    let [edit, setEdit] = useState(false);

    let [shipping_address_gstin, set_shipping_address_gstin] = useState(shippingAddress?.shipping_address_gstin) as any;
    let [shipping_address_line1, set_shipping_address_line1] = useState(shippingAddress?.shipping_address_line1) as any;
    let [shipping_address_line2, set_shipping_address_line2] = useState(shippingAddress?.shipping_address_line2) as any;
    let [shipping_address_city, set_shipping_address_city] = useState(shippingAddress?.shipping_address_city) as any;
    let [shipping_address_state, set_shipping_address_state] = useState(shippingAddress?.shipping_address_state) as any;
    let [shipping_address_zipcode, set_shipping_address_zipcode] = useState(shippingAddress?.shipping_address_zipcode) as any;
    let [shipping_address_country, set_shipping_address_country] = useState(shippingAddress?.shipping_address_country) as any;

    return (
        <div className="p-3 rounded mb-3" style={{ background: "#EEF3EF" }}>
            <div className="d-flex bg-dark text-light px-3 py-2 rounded align-items-center justify-content-between mb-2">
                <p className="h6 m-0">{shippingAddress != null ? <i className="bg-light rounded-circle p-1 fa fa-check-circle text-success"></i> : <i className="bg-light rounded-circle p-1 fa fa-exclamation-circle text-warning"></i>} Shipping Address</p>

                {edit ? (
                    <div>
                        <button
                            disabled={shipping_address_gstin == "" || shipping_address_line1 == "" || shipping_address_city == "" || shipping_address_state == "" || shipping_address_zipcode == "" || shipping_address_country == ""}
                            onClick={() => {
                                props.onSaved({
                                    shipping_address_gstin: shipping_address_gstin ?? shippingAddress?.shipping_address_gstin,
                                    shipping_address_line1: shipping_address_line1 ?? shippingAddress?.shipping_address_line1,
                                    shipping_address_line2: shipping_address_line2 ?? shippingAddress?.shipping_address_line2,
                                    shipping_address_city: shipping_address_city ?? shippingAddress?.shipping_address_city,
                                    shipping_address_state: shipping_address_state ?? shippingAddress?.shipping_address_state,
                                    shipping_address_zipcode: shipping_address_zipcode ?? shippingAddress?.shipping_address_zipcode,
                                    shipping_address_country: shipping_address_country ?? shippingAddress?.shipping_address_country,
                                });
                                setEdit(false);
                            }}
                            className="click  btn btn-success btn-sm p-0 px-1 me-1"
                        >
                            <i className="fa fa-save"></i> Save
                        </button>
                        <div onClick={() => setEdit(false)} className="click  btn btn-danger btn-sm p-0 px-1">
                            <i className="fa fa-times"></i>
                        </div>
                    </div>
                ) : (
                    <div
                        onClick={() => {
                            setEdit(true);
                            // set_shipping_address_gstin("");
                            // set_shipping_address_line1("");
                            // set_shipping_address_line2("");
                            // set_shipping_address_city("");
                            // set_shipping_address_state("");
                            // set_shipping_address_zipcode("");
                            // set_shipping_address_country("");
                        }}
                        className="click  btn btn-light btn-sm p-0 px-1"
                    >
                        <i className="fa fa-edit"></i>
                    </div>
                )}
            </div>
            <div className="px-2">
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">GSTIN </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_gstin ?? shippingAddress?.shipping_address_gstin} onChange={(e) => set_shipping_address_gstin(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_gstin ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Line 1 </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_line1 ?? shippingAddress?.shipping_address_line1} onChange={(e) => set_shipping_address_line1(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_line1 ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Line 2 </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_line2 ?? shippingAddress?.shipping_address_line2} onChange={(e) => set_shipping_address_line2(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_line2 ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">City </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_city ?? shippingAddress?.shipping_address_city} onChange={(e) => set_shipping_address_city(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_city ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">State </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_state ?? shippingAddress?.shipping_address_state} onChange={(e) => set_shipping_address_state(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_state ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Pincode </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_zipcode ?? shippingAddress?.shipping_address_zipcode} onChange={(e) => set_shipping_address_zipcode(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_zipcode ?? ""}</p>}</div>
                </div>
                <div className="row  d-flex align-items-center mb-2">
                    <div className="col-md-4 d-flex align-items-center justify-content-between pe-0">
                        <div className="h6 m-0">Country </div> :
                    </div>
                    <div className="col-md-8">{edit ? <input defaultValue={shipping_address_country ?? shippingAddress?.shipping_address_country} onChange={(e) => set_shipping_address_country(e.target.value)} type="text" className="form-control form-control-sm" /> : <p className="m-0  style={{textAlign: 'justify'}}fw-normal">{shippingAddress?.shipping_address_country ?? ""}</p>}</div>
                </div>
            </div>
        </div>
    );
}
