import { useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../../../app/auth";
import AuthController from "../../../app/controllers/AuthController";

export default function CheckEmail(props: any) {
    let {
        register,
        handleSubmit,
        formState: { errors, isDirty },
    } = useForm();

    let auth = useAuth();
    let navigate = useNavigate();

    let [responseError, setResponseError] = useState(null) as any;

    function onSubmit(data: any) {
        AuthController.checkEmail(data.email).then((res: any) => {
            console.log(res);
            if (res?.user == false) {
                setResponseError("Account Not Found");
            } else {
                if (res?.user == true) props.onSetEmail(data.email);

                props.onSetPasswordStatus(res?.hasPassword);
            }
        });
    }

    return (
        <form className="mb-5" onSubmit={handleSubmit(onSubmit)}>
            {responseError == null ? (
                <></>
            ) : (
                <div className="alert alert-danger fade  alert-dismissible show" role="alert">
                    {responseError}

                    <button type="button" className="btn-close" onClick={() => setResponseError(null)}></button>
                </div>
            )}

            <div className="mb-3">
                <input type="text" defaultValue={props?.email ?? ""} placeholder="Enter Email" className="form-control mb-1" {...register("email", { required: true })} />
                {errors?.email && <span className="text-danger text-start fw-bold ">*Email is Required</span>}
            </div>

            <button type="submit" className="btn btn-success my-3 w-100 border-0 d-flex justify-content-between align-items-center">
                Next <i className="fa fa-chevron-right"></i>
            </button>

            {/*

    <button type="submit" className="btn btn-success my-3 w-100 border-0">
        <i className="fa fa-shield-alt"></i> Login
    </button> */}
            {/* <button type="button" onClick={() => props.setIsLogin(false)} className="btn btn-outline-secondary mb-3 w-100 text-light alert-sucess">
        <i className="fa fa-user-plus"></i> Create Account
    </button> */}
        </form>
    );
}
