import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import OrderController from "../../../../app/controllers/OrderController";
import Spinner from "../../../components/common/spinner";

export default function ViewOrder() {
    let { id } = useParams();

    let [order, setOrder] = useState(null) as any;

    useEffect(() => {
        OrderController.show(id).then((res: any) => {
            // console.log(res);
            if (res?.message != null) return;

            setOrder(res);
        });
    }, []);

    function pay() {
        OrderController.pay(id).then((res: any) => {
            // console.log(res);
            if (res?.message != null) return;

            if (res.redirect) window.location.href = res.redirect;
            // setOrder(res);
        });
    }

    return order == null ? (
        <Spinner />
    ) : (
        <div>
            <div className="p-3 rounded mb-3" style={{ background: "#EEF3EF" }}>
                <div className="h6 d-flex bg-dark text-light px-3 py-2 rounded align-items-center justify-content-between mb-2">
                    <div className="">
                        <Link to={"../"} className="btn btn-outline-light btn-sm me-3 p-0 px-2">
                            <i className="fa fa-chevron-left"></i>
                        </Link>
                        <i className={`bg-light rounded-circle p-1 fa ${order.status.icon} text-${order.status.color}`}></i> Order #{order?.id}
                    </div>

                    <span className={"text-capitalize badge bg-" + order.status.color}>{order.status.key}</span>
                </div>
                <div className="px-3 text-dark">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="mb-3">
                                <div className="d-flex align-items-center justify-content-between mb-2">
                                    <label className="form-label h6 m-0">
                                        <i className="fa fa-shipping-fast"></i> Shipping Address
                                    </label>
                                </div>
                                <p className="">{[order?.shipping_address?.shipping_address_line1, order?.shipping_address?.shipping_address_line2, order?.shipping_address?.shipping_address_city, order?.shipping_address?.shipping_address_state, order?.shipping_address?.shipping_address_zipcode, order?.shipping_address?.shipping_address_country].filter((el: any) => (el != null ? el : null)).join(", ")}</p>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="mb-3">
                                <div className="d-flex align-items-center justify-content-between mb-2">
                                    <label className="form-label h6 m-0">
                                        <i className="fa fa-money-bill"></i> Billing Address
                                    </label>
                                </div>
                                <p className="">{[order?.billing_address?.billing_address_line1, order?.billing_address?.billing_address_line2, order?.billing_address?.billing_address_city, order?.billing_address?.billing_address_state, order?.billing_address?.billing_address_zipcode, order?.billing_address?.billing_address_country].filter((el: any) => (el != null ? el : null)).join(", ")}</p>
                            </div>
                        </div>

                        <div className="col-md-4">
                            <p className="h6">Transaction</p>

                            {order.payments.length == 0 ? (
                                <p className="h5 text-center fw-light py-4">No Transaction</p>
                            ) : (
                                order.payments.map((payment: any) => (
                                    <div key={payment.id} className="bg-light mb-2 rounded">
                                        <div className="d-flex align-items-center justify-content-between mb-2 p-2">
                                            <div className="">
                                                <i className={`bg-light rounded-circle p-1 fa ${payment.status.icon} text-${payment.status.color}`}></i>
                                                <span className="fw-bold">{payment.order_ref_id}</span>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            )}
                        </div>
                        <div className="col-md-8 border-start">
                            {order.recharges.map((recharge: any) => (
                                <div key={recharge.id} className="border-bottom mb-3">
                                    <div className="d-flex align-items-center justify-content-between ">
                                        <div className="mb-2 border-end">
                                            <div className="d-flex align-items-center justify-content-between mb-2">
                                                <label className="form-label h5 m-0"> Recharge</label>
                                            </div>
                                        </div>
                                        <div className="mb-2">
                                            <div className="d-flex align-items-center justify-content-between mb-2">
                                                <label className="form-label h6 m-0"> Plan</label>
                                            </div>
                                            <p className="bg-dark text-light rounded p-2 py-1 text-capitalize">{recharge?.plan_details?.title} Plan</p>
                                        </div>
                                        {recharge.purchase.number_of_devices > 0 ? (
                                            <div className="mb-2">
                                                <div className="d-flex align-items-center justify-content-between mb-2">
                                                    <label className="form-label h6 m-0"> Devices</label>
                                                </div>
                                                <p className="">
                                                    {recharge.purchase.number_of_devices} Devices {recharge.purchase.order_id == order.id ? <span className="badge bg-success">New</span> : <></>}
                                                </p>
                                            </div>
                                        ) : (
                                            <></>
                                        )}
                                        {recharge.purchase.number_of_licenses > 0 ? (
                                            <div className="mb-2">
                                                <div className="d-flex align-items-center justify-content-between mb-2">
                                                    <label className="form-label h6 m-0"> Licenses</label>
                                                </div>
                                                <p className="">
                                                    {recharge.purchase.number_of_licenses} Licenses {recharge.purchase.order_id == order.id ? <span className="badge bg-success">New</span> : <></>}
                                                </p>
                                            </div>
                                        ) : (
                                            <></>
                                        )}

                                        <div className="mb-2">
                                            <div className="d-flex align-items-center justify-content-between mb-2">
                                                <label className="form-label h6 m-0"> Days</label>
                                            </div>
                                            <p className="">{recharge.number_of_days} Days</p>
                                        </div>
                                    </div>
                                </div>
                            ))}

                            <div className="mb-2 light">
                                <div className="row align-items-center">
                                    <div className="col-md-6">
                                        <label className="form-label h6 m-0"> Shipping GST:</label>
                                    </div>
                                    <div className="col-md-6 text-end">
                                        <p className="h6 m-0"> {order.shipping_address.shipping_address_gstin}</p>
                                    </div>
                                </div>
                            </div>
                            {order?.total?.purchase > 0 ? (
                                <div className="mb-2 light">
                                    <div className="row align-items-center">
                                        <div className="col-md-6">
                                            <label className="form-label h6 m-0"> License Purchase Cost:</label>
                                        </div>
                                        <div className="col-md-6 text-end">
                                            <p className="h6 m-0"> {order?.total?.purchase}</p>
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <></>
                            )}

                            <div className="mb-2 light">
                                <div className="row align-items-center">
                                    <div className="col-md-6">
                                        <label className="form-label h6 m-0"> Recharge Cost:</label>
                                    </div>
                                    <div className="col-md-6 text-end">
                                        <p className="h6 m-0"> {order?.total?.recharge}</p>
                                    </div>
                                </div>
                            </div>
                            <div className="mb-2 light">
                                <div className="row align-items-center">
                                    <div className="col-md-6">
                                        <label className="form-label h6 m-0"> GST:</label>
                                    </div>
                                    <div className="col-md-6 text-end">
                                        <p className="h6 m-0"> {order?.total?.gst}</p>
                                    </div>
                                </div>
                            </div>
                            <div className="mb-2 light">
                                <div className="row align-items-center">
                                    <div className="col-md-6">
                                        <label className="form-label h6 m-0"> Total:</label>
                                    </div>
                                    <div className="col-md-6 text-end">
                                        <p className="h6 m-0"> {order?.total?.total}</p>
                                    </div>
                                </div>
                            </div>
                            {order.status.key == "pending" ? (
                                <div className="d-flex align-items-center justify-content-between mt-3">
                                    <div></div>
                                    {/* <button className="btn btn-danger btn-sm">
                                        <i className="fa fa-times"></i> Cancel Order
                                    </button> */}
                                    <button onClick={() => pay()} className="btn btn-success btn-sm">
                                        Proceed to Secure Payment
                                    </button>
                                </div>
                            ) : (
                                <></>
                            )}
                            {/* @if ($order->state == 0)
                    @endif */}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
