import { authToken } from "../auth";
import Fetch from "../utils/fetch.service";

export default class CompanyController {
    static async store(data: any) {
        // querystring.stringify(query);
        const response = await Fetch.post("/companies", { token: authToken(), body: data });
        return response;
    }

    static async update(company: any) {
        // querystring.stringify(query);
        const response = await Fetch.put("/companies/" + company.id, { token: authToken(), body: company });
        return response;
    }
}
