import { useEffect, useState } from "react";
import { Link, useLocation, useNavigate, useSearchParams } from "react-router-dom";
// import qs from "query-string";

import { Modal } from "bootstrap";
import Login from "./login";
import { useAuth } from "../../../app/auth";
import Register from "./register";

export default function LoginRegister() {
    let [isLogin, setIsLogin] = useState(true);

    // let qs

    let location = useLocation() as any;
    // let navigate = useNavigate();
    // const background = location.state || location.state?.background;

    // qs.get()
    // let

    let [searchParams, setSearchParams] = useSearchParams();
    let auth = useAuth();
    let [myModalEl, set_myModalEl] = useState(null) as any;
    let [myModal, set_myModal] = useState(null) as any;

    useEffect(() => {
        myModalEl = document.getElementById("loginModal") as any;
        set_myModalEl(myModalEl);
        myModal = new Modal(myModalEl) as any;
        set_myModal(myModal);

        myModalEl.addEventListener("hidden.bs.modal", function (event: any) {
            // console.log(event);
            setIsLogin(true);

            if (searchParams.get("login") != null) setSearchParams({});
            // navigate(location);
        });
    }, []);

    useEffect(() => {
        // if (auth.session) setSearchParams({});
        // console.log(auth.session);

        if (searchParams.get("login") != null && !auth?.session?.user) myModal?.show();

        myModal?.hide();
    }, [location, searchParams.get("login")]);
    // console.log(myModal.show());
    // console.log(location);

    return (
        <>
            {/* <Link to={'login'} state={{ backgound: location }} data-bs-toggle="modal" data-bs-target="#loginModal" className="btn btn-sm btn-secondary login-button">
                <i className="fa fa-sign-in-alt"></i> {isLogin ? "Login" : "Register"}
            </Link> */}
            <div className="modal fade show" id="loginModal" tabIndex={-1}>
                <div className={"modal-dialog modal-dialog modal-dialog-centered mx-auto"} style={{ maxWidth: 320 }}>
                    <div className="modal-content  card shadow">
                        <div className="gradient-border p-2"></div>

                        {isLogin ? (
                            <Login setIsLogin={setIsLogin} />
                        ) : (
                            <div className="modal-body text-light">
                                <Register setIsLogin={setIsLogin} />
                            </div>
                        )}
                        <p className="text-center small">
                            <a href="#" style={{ color: "#A7A8AD", textDecoration: "none" }}>
                                {" "}
                                Terms &amp; Condition{" "}
                            </a>{" "}
                            <span className="text-muted">|</span>
                            <a href="#" style={{ color: "#A7A8AD", textDecoration: "none" }}>
                                {" "}
                                Privacy Policy{" "}
                            </a>{" "}
                            <span className="text-muted">|</span>
                            <a href="#" style={{ color: "#A7A8AD", textDecoration: "none" }}>
                                {" "}
                                Legal Disclaimer{" "}
                            </a>
                        </p>
                        <div className="gradient-border p-2"></div>
                    </div>
                </div>
            </div>
            {/* <div className="modal-backdrop fade show"></div> */}
            <div className="modal fade" id="registerModal" tabIndex={-1}>
                <div className="modal-dialog modal-dialog modal-dialog-centered modal-lg">
                    <div className="modal-content card">
                        <div className="gradient-border p-2"></div>
                        <div className="gradient-border p-2"></div>
                    </div>
                </div>
            </div>
        </>
    );
}
