import { useState } from "react";
import { useNavigate } from "react-router-dom";
import OrderController from "../../../app/controllers/OrderController";

export default function OrderForm(props: any) {
    let selectedPlan = props?.selectedPlan;

    let [devicesCount, setDevicesCount] = useState(0);
    let [licensesCount, setLicensesCount] = useState(0);
    let [subscriptionDays, setSubscriptionDays] = useState("");

    let deviceCost = 9900;
    let licenseCost = 800;

    let shippingAddress = props?.shippingAddress;
    let billingAddress = props?.billingAddress;

    let recharge_cost = (Number(devicesCount) + Number(licensesCount)) * Number(selectedPlan?.cost_per_day) * Number(subscriptionDays);

    let sgst = Number(recharge_cost) * 0.18;
    let cgst = Number(recharge_cost) * 0.18;

    let total = Number(deviceCost) + Number(licenseCost) + Number(recharge_cost) + Number(sgst) + Number(cgst);

    let navigate = useNavigate();

    function checkoutOrder() {
        OrderController.store({
            plan_id: selectedPlan?.id,
            number_of_devices: devicesCount,
            number_of_licenses: licensesCount,
            number_of_days: subscriptionDays,
            shipping_address: shippingAddress,
            billing_address: billingAddress,
        }).then((order: any) => {
            console.log(order);

            if (order.id != null) navigate("/accounts/orders/" + order.id);
        });
    }

    let orderReady = shippingAddress != null && billingAddress != null && selectedPlan != null && (devicesCount != 0 || licensesCount != 0) && subscriptionDays != "";

    return (
        <div className="p-3 rounded" style={{ background: "#EEF3EF" }}>
            <div className="mb-3 bg-dark text-light px-3 py-2 rounded">
                <p className="h6 m-0">{orderReady ? <i className="bg-light rounded-circle p-1 fa fa-check-circle text-success"></i> : <i className="bg-light rounded-circle p-1 fa fa-exclamation-circle text-warning"></i>} Order</p>
            </div>
            <div className="">
                <div className="px-2">
                    <div className="mb-2">
                        <div className="d-flex align-items-center justify-content-between mb-1">
                            <label className="form-label h6 m-0">
                                <i className="fa fa-shipping-fast"></i> Shipping Address
                            </label>
                            <p className="m-0">
                                <strong>GSTIN: </strong>
                                {shippingAddress?.shipping_address_gstin}
                            </p>
                        </div>
                        <p className="">{[shippingAddress?.shipping_address_line1, shippingAddress?.shipping_address_line2, shippingAddress?.shipping_address_city, shippingAddress?.shipping_address_state, shippingAddress?.shipping_address_zipcode, shippingAddress?.shipping_address_country].filter((el: any) => (el != null ? el : null)).join(", ")}</p>
                    </div>
                    <div className="mb-2">
                        <label className="form-label h6 m-0">
                            <i className="fa fa-money-bill"></i> Billing Address
                        </label>
                        <p className="">{[billingAddress?.billing_address_line1, billingAddress?.billing_address_line2, billingAddress?.billing_address_city, billingAddress?.billing_address_state, billingAddress?.billing_address_zipcode, billingAddress?.billing_address_country].filter((el: any) => (el != null ? el : null)).join(", ")}</p>
                    </div>
                    <hr />
                    <div className="mb-2 light">
                        <div className="row align-items-center">
                            <div className="col-md-6">
                                <label className="form-label h6 m-0">
                                    <i className="fa fa-shopping-cart"></i> Plan:
                                </label>
                            </div>
                            <div className="col-md-6 text-center">{selectedPlan != null ? <p className="m-0 h6 bg-dark text-light rounded p-2 text-capitalize">{selectedPlan.title}</p> : <p className="text-center fw-bold text-danger mb-0">Plan not selected</p>}</div>
                        </div>
                    </div>
                    <div className="mb-2 ">
                        <div className="row align-items-center">
                            <div className="col-md-6">
                                <label className="form-label h6 m-0">
                                    <i className="fa fa-microchip"></i> Devices:
                                </label>
                            </div>
                            <div className="col-md-6">
                                <div className="input-group light ">
                                    <button disabled={devicesCount == 0} onClick={() => (devicesCount > 0 ? setDevicesCount(devicesCount - 10) : null)} type="button" className="btn btn-danger btn-sm">
                                        <i className="fa fa-minus"></i>
                                    </button>
                                    <div className="fw-bold form-control form-control-sm text-center">{devicesCount}</div>
                                    <button disabled={devicesCount == 100} onClick={() => (devicesCount < 100 ? setDevicesCount(devicesCount + 10) : null)} type="button" className="btn btn-success btn-sm">
                                        <i className="fa fa-plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="mb-2">
                        <div className="row align-items-center">
                            <div className="col-md-6">
                                <label className="form-label h6 m-0">
                                    <i className="fa fa-file-certificate"></i> Licenses:
                                </label>
                            </div>
                            <div className="col-md-6">
                                <div className="input-group light ">
                                    <button disabled={licensesCount == 0} onClick={() => (licensesCount > 0 ? setLicensesCount(licensesCount - 10) : null)} type="button" className="btn btn-danger btn-sm">
                                        <i className="fa fa-minus"></i>
                                    </button>
                                    <div className="fw-bold form-control form-control-sm text-center ">{licensesCount}</div>
                                    <button disabled={licensesCount == 100} onClick={() => (licensesCount < 100 ? setLicensesCount(licensesCount + 10) : null)} type="button" className="btn btn-success btn-sm">
                                        <i className="fa fa-plus"></i>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    {devicesCount > 0 || licensesCount > 0 ? (
                        <div className="mb-2 light">
                            <div className="row align-items-center">
                                <div className="col-md-6">
                                    <label className="form-label h6 m-0">
                                        <i className="fa fa-calendar-alt"></i> Recharge:
                                    </label>
                                </div>
                                <div className="col-md-6">
                                    <select value={subscriptionDays} onChange={(e) => setSubscriptionDays(e.target.value)} className="fw-bold text-center form-select form-select-sm">
                                        <option value="">--- Select ---</option>
                                        <option value="91">91 Days</option>
                                        <option value="183">183 Days</option>
                                        <option value="365">365 Days</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}

                    {devicesCount > 0 ? (
                        <div className="mb-2 light">
                            <div className="row align-items-center">
                                <div className="col-md-8">
                                    <label className="form-label h6 m-0"> Device Cost:</label>  {deviceCost} x {devicesCount} Unit
                                </div>
                                <div className="col-md-4 text-end">
                                    <p className="h6 m-0"> {deviceCost * devicesCount}</p>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}
                    {licensesCount > 0 ? (
                        <div className="mb-2 light">
                            <div className="row align-items-center">
                                <div className="col-md-8">
                                    <label className="form-label h6 m-0"> License Cost:</label>  {licenseCost} x {licensesCount} Unit
                                </div>
                                <div className="col-md-4 text-end">
                                    <p className="h6 m-0"> {licenseCost * licensesCount}</p>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}
                    {subscriptionDays != "" ? (
                        <div className="mb-2 light">
                            <div className="row align-items-center">
                                <div className="col-md-6">
                                    <label className="form-label h6 m-0">
                                        {" "}
                                        Recharge Cost:
                                        <button type="button" className="btn btn-sm p-0 px-1" data-bs-toggle="collapse" data-bs-target="#recharge_collapse">
                                            <i className="fa fa-question-circle"></i>
                                        </button>
                                    </label>
                                </div>
                                <div className="col-md-6 text-end">
                                    <p className="h6 m-0"> {recharge_cost}</p>
                                </div>
                                {/* <div className="col-md-12 collapse show" id="recharge_collapse">
                                    <div className="row align-items-center ps-1">
                                        <div className="col-md-6">
                                            <label className="form-label m-0"> {"month"}:</label>
                                        </div>
                                        <div className="col-md-6 text-end">
                                            <p className="m-0"> {0}</p>
                                        </div>
                                    </div>
                                </div> */}
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}

                    {sgst + cgst > 0 ? (
                        <div className="mb-2 light">
                            <div className="row align-items-center">
                                <div className="col-md-6">
                                    <label className="form-label h6 m-0">
                                        {" "}
                                        GST:
                                        <button type="button" className="btn btn-sm p-0 px-1" data-bs-toggle="collapse" data-bs-target="#gst_collapse">
                                            <i className="fa fa-question-circle"></i>
                                        </button>
                                    </label>
                                </div>
                                <div className="col-md-6 text-end">
                                    <p className="h6 m-0"> {sgst + cgst}</p>
                                </div>
                                <div className="col-md-12 collapse show" id="gst_collapse">
                                    {sgst > 0 ? (
                                        <div className="row align-items-center ps-1">
                                            <div className="col-md-6">
                                                <label className="form-label m-0"> SGST:</label>
                                            </div>
                                            <div className="col-md-6 text-end">
                                                <p className="m-0"> {sgst}</p>
                                            </div>
                                        </div>
                                    ) : (
                                        <></>
                                    )}
                                    {cgst > 0 ? (
                                        <div className="row align-items-center ps-1">
                                            <div className="col-md-6">
                                                <label className="form-label m-0"> CGST:</label>
                                            </div>
                                            <div className="col-md-6 text-end">
                                                <p className="m-0"> {cgst}</p>
                                            </div>
                                        </div>
                                    ) : (
                                        <></>
                                    )}
                                </div>
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}

                    {subscriptionDays != "" && total > 0 ? (
                        <div className="mb-2 light">
                            <div className="row align-items-center">
                                <div className="col-md-6">
                                    <label className="form-label h6 m-0"> Total:</label>
                                </div>
                                <div className="col-md-6 text-end">
                                    <p className="h6 m-0"> {total}</p>
                                </div>
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}

                    {/* {selectedPlan == null ? <p className="text-danger fw-bold text-center mb-3">Plan Not Selected</p> : <></>} */}

                    <div className="mt-3">
                        <button onClick={() => checkoutOrder()} disabled={!orderReady} className="btn btn-success w-100" type="button">
                            Proceed to Secure Payment
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
