import { useState } from "react";
import { useForm } from "react-hook-form";
import { useAuth } from "../../../../app/auth";
import CompanyController from "../../../../app/controllers/CompanyController";

export default function OrganizationForm(props: any) {
    let organization = props?.selectedOrganization;

    let {
        register,
        handleSubmit,
        formState: { errors, isDirty },
    } = useForm();

    let auth = useAuth();

    let [responseMessage, setResponseMessage] = useState(null) as any;

    function onSubmit(data: any) {
        // console.log(data);

        if (organization == null) {
            CompanyController.store(data).then((res: any) => {
                // console.log(res);
                if (res?.message != null) setResponseMessage(res?.message);

                if (res?.id != null) {
                    setResponseMessage(null);
                    props.onSaved(res);
                    window.location.reload();
                }
            });
        } else {
            CompanyController.update(Object.assign(data, { id: organization?.id })).then((res: any) => {
                // console.log(res);
                if (res?.message != null) setResponseMessage(res?.message);

                if (res?.id != null) {
                    setResponseMessage(null);
                    props.onSaved(res);
                    window.location.reload();
                }
            });
        }
    }

    return (
        <form className="p-3 text-dark" onSubmit={handleSubmit(onSubmit)}>
            <p className="h3">
                {props?.selectedOrganization == null ? (
                    <>
                        <i className="fad fa-plus"></i> Create an Organization
                    </>
                ) : (
                    <>
                        <i className="fad fa-edit"></i> Edit Organization Details - {props?.selectedOrganization?.domain}.{process.env.REACT_APP_HOST_NAME}
                    </>
                )}
            </p>
            <hr />
            <div className="">
                {responseMessage == null ? (
                    <></>
                ) : (
                    <div className="alert alert-danger fade  alert-dismissible show" role="alert">
                        {responseMessage}

                        <button type="button" className="btn-close" onClick={() => setResponseMessage(null)}></button>
                    </div>
                )}
                <div className="p-3 rounded mb-3" style={{ background: "#EEF3EF" }}>
                    <p className="h6 bg-dark text-light px-3 mb-3 py-2 rounded">{true == null ? <i className="bg-light rounded-circle p-1 fa fa-exclamation-circle text-warning"></i> : <i className="bg-light rounded-circle p-1 fa fa-check-circle text-success"></i>} Organization Details</p>
                    <div className="row mb-3">
                        <label className="col-md-3 col-form-label fw-bold" htmlFor="">
                            Organization Name:
                        </label>
                        <div className="col-md-9">
                            <input type="text" className="form-control" {...register("name", { value: props?.selectedOrganization?.name, required: true })} />
                            {errors?.name && <span className="mt-1 text-danger text-start fw-bold ">*Name is Required</span>}
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label className="col-md-3 col-form-label fw-bold" htmlFor="">
                            GSTIN:
                        </label>
                        <div className="col-md-9">
                            <input type="text" className="form-control" {...register("gstin", { value: props?.selectedOrganization?.gstin, required: true })} />
                            {errors?.gstin && <span className="mt-1 text-danger text-start fw-bold ">*GSTIN is Required</span>}
                        </div>
                    </div>
                    {props?.selectedOrganization == null ? (
                        <div className="row mb-3">
                            <label className="col-md-3 col-form-label fw-bold" htmlFor="">
                                Sub-Domain:
                            </label>
                            <div className="col-md-9">
                                <div className="input-group">
                                    <span className="input-group-text">
                                        <i className="fa fa-building"></i>
                                    </span>
                                    <input type="text" className="form-control" {...register("domain", { required: true })} />

                                    <span className="input-group-text">.{process.env.REACT_APP_HOST_NAME}</span>
                                </div>
                                {errors?.domain && <span className="mt-1 text-danger text-start fw-bold ">*Domain is Required</span>}
                            </div>
                        </div>
                    ) : (
                        <></>
                    )}
                </div>
                <div className="p-3 rounded mb-3" style={{ background: "#EEF3EF" }}>
                    <p className="h6 bg-dark text-light px-3 mb-3 py-2 rounded">{true == null ? <i className="bg-light rounded-circle p-1 fa fa-exclamation-circle text-warning"></i> : <i className="bg-light rounded-circle p-1 fa fa-check-circle text-success"></i>} Address Details</p>

                    <div className="mb-3">
                        <label className="form-label fw-bold" htmlFor="">
                            Building, Area, Street, Sector:
                        </label>
                        <input type="text" className="form-control" {...register("address.line1", { value: props?.selectedOrganization?.address?.line1, required: true })} />
                        {errors?.address?.line1 && <span className="mt-1 text-danger text-start fw-bold ">*Building, Area, Street, Sector: is Required</span>}
                    </div>
                    <div className="mb-3">
                        <label className="form-label fw-bold" htmlFor="">
                            Landmark (Optional):
                        </label>
                        <input type="text" className="form-control" {...register("address.line2", { value: props?.selectedOrganization?.address?.line2 })} />
                    </div>
                    <div className="row">
                        <div className="col-md-6">
                            <div className="mb-3">
                                <label className="form-label fw-bold" htmlFor="">
                                    Town / City
                                </label>
                                <input type="text" className="form-control" {...register("address.city", { value: props?.selectedOrganization?.address?.city, required: true })} />
                                {errors?.address?.city && <span className="mt-1 text-danger text-start fw-bold ">*Town / City is Required</span>}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="mb-3">
                                <label className="form-label fw-bold" htmlFor="">
                                    Zipcode
                                </label>
                                <input type="text" className="form-control" {...register("address.zipcode", { value: props?.selectedOrganization?.address?.zipcode, required: true })} />
                                {errors?.address?.zipcode && <span className="mt-1 text-danger text-start fw-bold ">*Zipcode is Required</span>}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="mb-3">
                                <label className="form-label fw-bold" htmlFor="">
                                    State
                                </label>
                                <input type="text" className="form-control" {...register("address.state", { value: props?.selectedOrganization?.address?.state, required: true })} />
                                {errors?.address?.state && <span className="mt-1 text-danger text-start fw-bold ">*State is Required</span>}
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="mb-3">
                                <label className="form-label fw-bold" htmlFor="">
                                    Country
                                </label>
                                <input type="text" className="form-control" {...register("address.country", { value: props?.selectedOrganization?.address?.country, required: true })} />
                                {errors?.address?.country && <span className="mt-1 text-danger text-start fw-bold ">*Country is Required</span>}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* {props?.selectedOrganization == null ? ( */}
            <button className="btn btn-success">
                <i className="fa fa-plus"></i> Submit
            </button>
            {/* ) : (
                <></>
            )} */}
        </form>
    );
}
