import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import PlanController from "../../../../app/controllers/PlanController";
import Spinner from "../../../components/common/spinner";
import plan_business from "./../../../../assets/images/plan_business.jpeg";
import plan_enterprise from "./../../../../assets/images/plan_enterprise.jpeg";

export default function Pricing() {
    let [plans, setPlans] = useState(null) as any;
    useEffect(() => {
        PlanController.index().then((response: any) => {
            console.log(response);
            setPlans(response);
        });
    }, []);

    return plans == null ? (
        <Spinner style={{ height: 500 }} />
    ) : (
        <>
            <div className="p-3">
                <div className="row">
                    <div className="col-md-6">
                        <div className="card">
                            <div className="card-body">
                                <p className="h2">Tracking Device (STRADA PLUS)</p>
                                <p className="h5">Cost: Rs. 9900</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="card">
                            <div className="card-body">
                                <p className="h2">Tracking License</p>
                                <p className="h5">Cost: Rs. 800</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {plans == null ? (
                <Spinner />
            ) : (
                <table className="table table-striped m-0">
                    <tbody>
                        <tr className="head">
                            <td className="title-col fw-bold" style={{ background: "#f5f7f8" }}>
                                <p className="display-4 fw-normals">Plans</p>
                            </td>
                            {plans?.map((plan: any) => {
                                let style = {};
                                let image = "";

                                if (plan.title == "business") {
                                    style = { background: "#5e7479", borderRightColor: "#5e7479" };
                                    image = plan_business;
                                }
                                if (plan.title == "enterprise") {
                                    style = { background: "#3d5257" };
                                    image = plan_enterprise;
                                }

                                return (
                                    <td key={plan.id} className="fw-bold text-light" style={style}>
                                        <div className="display-5 fw-normal mb-3 d-flex align-items-center">
                                            <div className="me-1">
                                                <img className="rounded-circle shadow" height="40px" src={image} alt="" />
                                            </div>
                                            <p className="m-0 mt-2 text-capitalize">{plan.title}</p>
                                        </div>
                                        <Link to="/checkout" className="btn btn-light fw-bold px-4 mb-2" style={{ color: "#3d5257", borderRadius: 10 }}>
                                            Start Now
                                        </Link>
                                    </td>
                                );
                            })}
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Deliveries Per Day</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            {plans?.map((plan: any) => (
                                <td key={plan.id} className="text-center fw-bold text-muted h6">
                                    {plan.deliveries_per_day == 0 ? (
                                        <>
                                            Unlimited <i className="fa fa-infinity"></i>
                                        </>
                                    ) : (
                                        <>Maximum {plan.deliveries_per_day}</>
                                    )}
                                </td>
                            ))}
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">User Accounts</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            {plans?.map((plan: any) => (
                                <td key={plan.id} className="text-center fw-bold text-muted h6">
                                    <p className="text-muted">{plan.active_manager_accounts} Manager</p>
                                    <p className="text-muted">{plan.active_operator_accounts} Operator</p>
                                    <p className="text-muted">{plan.active_management_accounts} Management</p>
                                </td>
                            ))}
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Warehouses</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            {plans?.map((plan: any) => (
                                <td key={plan.id} className="text-center fw-bold text-muted h6">
                                    {plan.active_warehouse_count == 0 ? (
                                        <>
                                            Unlimited <i className="fa fa-infinity"></i>
                                        </>
                                    ) : (
                                        <>{plan.active_warehouse_count} Warehouses</>
                                    )}
                                </td>
                            ))}
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Vehicles</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            {plans?.map((plan: any) => (
                                <td key={plan.id} className="text-center fw-bold text-muted h6">
                                    {plan.active_vehicles_count == 0 ? (
                                        <>
                                            Unlimited <i className="fa fa-infinity"></i>
                                        </>
                                    ) : (
                                        <>{plan.active_vehicles_count} Vehicles</>
                                    )}
                                </td>
                            ))}
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Drivers</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            {plans?.map((plan: any) => (
                                <td key={plan.id} className="text-center fw-bold text-muted h6">
                                    {plan.active_drivers_count == 0 ? (
                                        <>
                                            Unlimited <i className="fa fa-infinity"></i>
                                        </>
                                    ) : (
                                        <>{plan.active_drivers_count} Drivers</>
                                    )}
                                </td>
                            ))}
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Map Tiles</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">OSM</td>
                            <td className="text-center fw-bold text-muted h6">Google</td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Location Search Engine</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">OSM</td>
                            <td className="text-center fw-bold text-muted h6">Google</td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Routing Accuracy</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">Medium</td>
                            <td className="text-center fw-bold text-muted h6">High</td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Bulk Import Deliveries </p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">End Customer Notifications</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Live Asset Tracking</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Realtime Route Estimation</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-times text-danger"></i>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Realtime Danger Alerts</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-times text-danger"></i>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                        </tr>
                        <tr>
                            <td className="title-col">
                                <p className="h5 m-0">Realtime Danger Alerts</p>
                                <p className="m-0 collapse show small">Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis pariatur recusandae, facere quibusdam officia velit commodi.</p>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-times text-danger"></i>
                            </td>
                            <td className="text-center fw-bold text-muted h6">
                                <i className="fa fa-check text-success"></i>
                            </td>
                        </tr>
                        <tr className="." style={{ height: 120, verticalAlign: "bottom" }}>
                            <td className="title-col" style={{ background: "#f5f7f8" }}>
                                <p className="h1">Choose a plan</p>
                                <p className=".">
                                    <a href="" className=".">
                                        Want more details ?
                                    </a>
                                </p>
                            </td>
                            {plans?.map((plan: any) => {
                                let style = {};

                                if (plan.title == "business") {
                                    style = { background: "#5e7479", borderRightColor: "#5e7479" };
                                }
                                if (plan.title == "enterprise") {
                                    style = { background: "#3d5257" };
                                }

                                return (
                                    <td key={plan.id} className="fw-bold text-light text-center" style={style}>
                                        <div className="h3 mb-3">
                                            <p className="m-0 mt-2"> {plan.cost_per_day} / Device per day</p>
                                        </div>
                                        <Link to="/checkout" className="btn btn-light fw-bold px-4 mb-2" style={{ color: "#3d5257", borderRadius: 10 }}>
                                            Start Now
                                        </Link>
                                    </td>
                                );
                            })}
                        </tr>
                    </tbody>
                </table>
            )}
        </>
    );
}
