import { authCompany, authToken } from "../auth";
import Fetch from "../utils/fetch.service";

import querystring from "query-string";

export default class RechargeController {
    static async index(query: any = {}) {
        // querystring.stringify(query);
        // console.log(querystring.stringify(query))
        const response = await Fetch.get("/" + authCompany()?.domain + "/recharges?" + querystring.stringify(query), { token: authToken() });
        return response;
    }
}
