import { Link, useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useAuth } from "../../app/auth";
import LoginRegister from "../pages/login-register";
import logo from "./../../assets/images/logo.png";
import snpLogo from "./../../assets/images/snp.svg";

export default function Navbar() {
    let location = useLocation() as any;

    let auth = useAuth();

    let navigate = useNavigate();

    function signOut() {
        auth.signout(function () {
            navigate("/");
        });
    }

    let [searchParams, setSearchParams] = useSearchParams();

    return (
        <nav className="d-flex align-items-center justify-content-between">
            <div className="d-flex py-3 align-items-center flex-wrap">
                <Link to={"/"}>
                    <img src={logo} height="40px" alt="" />
                </Link>

                <p className="m-0 px-2 h6" style={{ color: "#2BB149" }}>
                    Geo vehicle traffic status <br />
                    and transit analysis
                </p>
            </div>

            <div className="d-flex align-items-center flex-wrap-reverse">
                {auth.session?.company ? (
                    <div className="me-3  border rounded py-1 px-3">
                        <div className="d-flex align-items-center">
                            <div className="h3 me-3 mb-0">
                                <i className="fa fa-building"></i>
                            </div>
                            <div className="">
                                <p className="m-0 h6">{auth.session.company.name}</p>
                                <a href={window.location.protocol + "//" + auth.session.company.domain + "." + process.env.REACT_APP_HOST_NAME + "/login?email=" + auth.session.user.email} target="_blank" className="m-0 text-warning click">
                                    {auth.session.company.domain}.{process.env.REACT_APP_HOST_NAME}
                                </a>
                            </div>
                        </div>
                    </div>
                ) : (
                    <></>
                )}
                <div className="me-3">
                    {auth.session?.user ? (
                        <>
                            <p className="m-0 h6 text-light">
                                <i className="fa fa-user-shield"></i> {auth.session.user.name}
                            </p>
                            <div className="dropdown text-end ">
                                <div className="dropdown-toggle text-warning click" data-bs-offset="10,10" data-bs-toggle="dropdown">
                                    Account
                                </div>
                                <div className="dropdown-menu dropdown-menu-end small pt-0" aria-labelledby="triggerId">
                                    <a className="btn-sm h6 dropdown-header bg-dark text-light m-2">
                                        <i className="fa fa-user-shield"></i> {auth.session.user.name}
                                    </a>
                                    {/* {{-- <div className="dropdown-divider my-0"></div> --}} */}
                                    <Link to={"/accounts/profile"} className="btn-sm dropdown-item">
                                        <i className="fa fa-id-card"></i> Profile
                                    </Link>
                                    <Link to={"/accounts/dashboard"} className="btn-sm dropdown-item">
                                        <i className="fa fa-tachometer-alt"></i> Dashboard
                                    </Link>
                                    <Link to={"/accounts/orders"} className="btn-sm dropdown-item">
                                        <i className="fa fa-file-invoice"></i> Orders
                                    </Link>
                                    <button onClick={() => signOut()} className="btn-sm dropdown-item text-danger">
                                        <i className="fa fa-sign-out"></i> Logout
                                    </button>
                                </div>
                            </div>
                        </>
                    ) : (
                        <button onClick={() => setSearchParams({ login: "" })} className="btn btn-sm btn-secondary login-button">
                            <i className="fa fa-sign-in-alt"></i> Login
                        </button>
                    )}
                    <LoginRegister />
                </div>
                <img className="snp-logo mb-1" src={snpLogo} height="45px" alt="" />
            </div>
        </nav>
    );
}
