import Fetch from "../utils/fetch.service";

export default class AuthController {
    static async checkEmail(email: string) {
        var response = await Fetch.post("/check-email", { body: { email: email } });

        return response;
    }

    static async login(body: any) {
        var response = await Fetch.post("/login", { body: body });

        return response;
    }

    static async setPasswordAndLogin(body: any) {
        var response = await Fetch.post("/set-password-and-login", { body: body });

        return response;
    }
    static async register(body: any) {
        var response = await Fetch.post("/register", { body: body });

        return response;
    }

    static async updateProfile(body: any) {
        var response = await Fetch.post("/update-profile", { body: body });

        return response;
    }
}
