import { Modal } from "bootstrap";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useAuth } from "../../../app/auth";
import Fetch from "../../../app/utils/fetch.service";
import CheckEmail from "./checkEmail";
import EnterNewPasswordAndLogin from "./enterNewPasswordAndLogin";
import EnterPasswordAndLogin from "./enterPasswordAndLogin";

export default function Login(props: any) {
    let [searchParams, setSearchParams] = useSearchParams();

    // let [errors, setErrors] = useState(null) as any;

    // console.log(auth);

    let [email, setEmail] = useState(null) as any;

    let [passwordStatus, setPasswordStatus] = useState(null) as any;

    return (
        <div className="modal-body px-4  text-light">
            <div id="loginCarousel" className="carousel slide" data-bs-slide="false" data-bs-ride="false" data-bs-interval="false">
                <div className="carousel-inner" role="listbox">
                    <div className="carousel-item active">
                        <p className="display-6 py-5 text-center">
                            <i className="fa fa-user-shield"></i> Login
                        </p>
                        {searchParams.get("login") != "" ? (
                            <div className="text-start alert alert-warning alert-dismissible fade show" role="alert">
                                <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                Please Login to Continue
                            </div>
                        ) : (
                            <></>
                        )}

                        {passwordStatus == null ? (
                            <CheckEmail email={email} onSetEmail={(email: any) => setEmail(email)} onSetPasswordStatus={(status: any) => setPasswordStatus(status)} />
                        ) : (
                            <>
                                <div className="mb-3">
                                    <div className="form-control fw-bold" style={{ cursor: "text" }} onClick={() => setPasswordStatus(null)}>
                                        {email}
                                    </div>
                                </div>
                                {passwordStatus == true ? <EnterPasswordAndLogin email={email} /> : <EnterNewPasswordAndLogin email={email} />}
                            </>
                        )}

                        <div className="d-flex align-items-center justify-content-center">
                            <p className="text-center m-0">Don't Have an Account?</p>
                            <button className="btn btn-sm btn-link py-0 text-warning" type="button" onClick={() => props.setIsLogin(false)}>
                                Register
                            </button>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <div className="d-flex align-items-center justify-content-between">
                            <a className="btn btn-sm btn-outline-light border-0" href="#" data-bs-target="#loginCarousel" data-bs-slide="prev">
                                <i className="fa fa-chevron-left"></i>
                            </a>

                            <p className="h5 py-4 fw-normal">Forgot Password</p>
                        </div>

                        <form>
                            <div className="mb-3">
                                <input type="text" placeholder="Enter Admin Email" className="form-control" />
                            </div>
                            <button type="submit" className="btn btn-success w-100 mb-3">
                                <i className="fa fa-envelope"></i> Send Reset Mail
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}
