import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useAuth } from "../../../app/auth";
import OrderController from "../../../app/controllers/OrderController";
import PlanController from "../../../app/controllers/PlanController";
import BillingAddress from "./billingAddress";
import OrderForm from "./orderForm";
import ShippingAddress from "./shippingAddress";

export default function Checkout() {
    let [plans, setPlans] = useState(null) as any;

    let [selectedPlan, setSelectedPlan] = useState(null) as any;

    let [shippingAddress, setShippingAddress] = useState(null) as any;
    let [billingAddress, setBillingAddress] = useState(null) as any;

    let auth = useAuth();

    let navigate = useNavigate();

    useEffect(() => {
        PlanController.index().then((response: any) => {
            console.log(response);
            setPlans(response);
        });

        if (auth.session?.company == null) {
            navigate("/accounts");
            // console.log(auth.session.company);
            // setShippingAddress = auth.session.company.address;
        }
        setShippingAddress({
            shipping_address_gstin: auth?.session?.company?.gstin,
            shipping_address_line1: auth?.session?.company?.address.line1,
            shipping_address_line2: auth?.session?.company?.address.line2,
            shipping_address_city: auth?.session?.company?.address.city,
            shipping_address_state: auth?.session?.company?.address.state,
            shipping_address_zipcode: auth?.session?.company?.address.zipcode,
            shipping_address_country: auth?.session?.company?.address.country,
        });
    }, []);

    return (
        <main className="p-3 text-dark">
            <div className="d-flex align-items-center justify-content-between mb-3">
                <p className="h3 fw-normal m-0">
                    <Link to="../" className="btn btn-dark btn-sm mb-1">
                        <i className="fa fa-chevron-left"></i>
                    </Link>{" "}
                    Order Checkout
                </p>

                <div className="text-end">
                    {/* {{-- <p className="m-0 h5">EssenRFID</p> --}} */}
                    <p className="m-0 h6 fw-normal">
                        <i className="fa fa-user"></i> {auth.session?.company?.name}
                    </p>
                    <p className="m-0 small fw-normal">
                        {auth.session?.company?.domain}.{process.env.REACT_APP_HOST_NAME}
                    </p>
                </div>
            </div>

            <div className="row">
                <div className="col-md-8">
                    <div className="p-3 rounded mb-3" style={{ background: "#EEF3EF" }}>
                        <p className="h6 bg-dark text-light px-3 mb-3 py-2 rounded">{selectedPlan == null ? <i className="bg-light rounded-circle p-1 fa fa-exclamation-circle text-warning"></i> : <i className="bg-light rounded-circle p-1 fa fa-check-circle text-success"></i>} Select Plan</p>
                        <div className="row plan-list">
                            {plans?.map((plan: any) => (
                                <div className="col-md-6" key={plan.id}>
                                    <label onClick={() => setSelectedPlan(plan)} className={"plan-card mb-3 " + (selectedPlan?.id == plan.id ? "active" : "")}>
                                        <div className="d-flex align-items-center  ">
                                            {/* <input type="radio" className="mt-1" wire:model="plan_id" required value="{ $loop.index }}" style="height: 20px;width:20px" */}
                                            <p className=" m-0 mx-2 h4 text-capitalize" style={{ color: "#085d36" }}>
                                                {plan.title} Plan
                                            </p>
                                        </div>
                                        <ul className="m-0 mt-2 ps-4 h6 fw-normal">
                                            <li className="small mb-1">
                                                <strong>Deliveries for Day: </strong>
                                                {plan.deliveries_per_day == 0 ? "Unlimited" : <>{plan.deliveries_per_day} Deliveries Each Device</>}
                                            </li>
                                            <li className="small mb-1">
                                                <strong>Cost for Day: </strong>  {plan.cost_per_day} Each Device
                                            </li>
                                        </ul>
                                    </label>
                                </div>
                            ))}
                        </div>
                    </div>

                    <div className="row">
                        <div className="col-md-6">
                            <ShippingAddress shippingAddress={shippingAddress} onSaved={setShippingAddress} />
                        </div>
                        <div className="col-md-6">
                            <BillingAddress billingAddress={billingAddress} shippingAddress={shippingAddress} onSaved={setBillingAddress} />
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <OrderForm shippingAddress={shippingAddress} billingAddress={billingAddress} selectedPlan={selectedPlan} />
                </div>
            </div>
        </main>
    );
}
