import "leaflet-draw";
import { useNavigate } from "react-router-dom";
// import draw from "leaflet-draw";

export default class LeafletMap {
    map = null as any;
    trucks = [] as any;
    deliveryPoints = [] as any;
    apiRoute = L.geoJSON();
    trackingRoute = L.geoJSON();
    layerControl = null as any;

    baseMaps = {
        "Open Street Map": L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", { attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors' }),
        // "Google Street": L.tileLayer('http://www.google.cn/maps/vt?lyrs=s@189&gl=cn&x={x}&y={y}&z={z}', { attribution: 'google' }),
        // "Open Topo Map": L.tileLayer('https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png', { maxZoom: 17, attribution: 'Map data: &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, <a href="http://viewfinderpanoramas.org">SRTM</a> | Map style: &copy; <a href="https://opentopomap.org">OpenTopoMap</a> (<a href="https://creativecommons.org/licenses/by-sa/3.0/">CC-BY-SA</a>)' }),
        // "Stadia_AlidadeSmooth": L.tileLayer('https://tiles.stadiamaps.com/tiles/alidade_smooth/{z}/{x}/{y}{r}.png', { maxZoom: 20, attribution: '&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors' }),
        // "Stadia_OSMBright": L.tileLayer('https://tiles.stadiamaps.com/tiles/osm_bright/{z}/{x}/{y}{r}.png', { maxZoom: 20, attribution: '&copy; <a href="https://stadiamaps.com/">Stadia Maps</a>, &copy; <a href="https://openmaptiles.org/">OpenMapTiles</a> &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors' }),
        // "Esri_WorldStreetMap": L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}', { attribution: 'Tiles &copy; Esri &mdash; Source: Esri, DeLorme, NAVTEQ, USGS, Intermap, iPC, NRCAN, Esri Japan, METI, Esri China (Hong Kong), Esri (Thailand), TomTom, 2012' }),
        Street: L.tileLayer("http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}", {
            maxZoom: 20,
            subdomains: ["mt0", "mt1", "mt2", "mt3"],
        }),
        Hybrid: L.tileLayer("http://{s}.google.com/vt/lyrs=s,h&x={x}&y={y}&z={z}", {
            maxZoom: 20,
            subdomains: ["mt0", "mt1", "mt2", "mt3"],
        }),
        Satellite: L.tileLayer("http://{s}.google.com/vt/lyrs=s&x={x}&y={y}&z={z}", {
            maxZoom: 20,
            subdomains: ["mt0", "mt1", "mt2", "mt3"],
        }),
        Terrain: L.tileLayer("http://{s}.google.com/vt/lyrs=p&x={x}&y={y}&z={z}", {
            maxZoom: 20,
            subdomains: ["mt0", "mt1", "mt2", "mt3"],
        }),
    };

    overlayLayers = {} as any;

    icons = {
        truck: (color: any = null) => {
            return L.divIcon({
                html: `<div><i class="fa fa-truck fa-3x text-${color} truck-marker" style="text-shadow:0 0 5px rgba(0,0,0,.15)"></i></div>`,
                iconSize: [20, 20],
                className: "marker-icon",
            });
        },
        device: (color = null) => {
            return L.divIcon({
                html: `<div><i class="fa fa-microchip fa-3x text-${color} device-marker" style="text-shadow:0 0 5px rgba(0,0,0,.15)"></i></div>`,
                iconSize: [20, 20],
                className: "marker-icon",
            });
        },
        deliveryPoint: (config = { color: null, index: null }) => {
            var data = config.index ? config.index : `<i class="fa fa-cube text-${config.color}"></i>`;
            return L.divIcon({
                html: `
                    <div class="text-center delivery-marker">
                        <div class=" count text-dark" style="background: rgba(255,255,255,.8)">${data}</div>
                        <div><i class="fa fa-map-marker fa-4x text-${config.color}" style="text-shadow:0 0 5px rgba(0,0,0,.15)"></i></div>
                    </div>
                    `,
                popupAnchor: [0, -40],
                iconSize: [50, 100],
                className: "marker-icon",
            });
        },
    };

    constructor(container = "leafletMap") {
        if (this!.map != undefined || this!.map != null) {
            this.map.off();
            this.map.remove();
        }
        let baseMapIndex = 0 as any;
        baseMapIndex = localStorage.getItem("map.baseMap") ?? 0;
        var layers = [Object.values(this.baseMaps)[baseMapIndex]];
        this.map = L.map(container, { layers: layers });
        L.control.scale().addTo(this.map);
        this.layerControl = L.control.layers(this.baseMaps).addTo(this.map);
    }

    show() {
        var LMap = this;
        var map = this.map;

        let mapCenter = localStorage.getItem("map.center") as any;
        let mapZoom = localStorage.getItem("map.zoom") as any;
        // this.map.setView([0, 0], 1)
        this.map.setView(JSON.parse(mapCenter) || [0, 0], mapZoom || 2);

        // this.draw.controls()
        // this.draw.createPolygon();
        // this.draw.actions()
        // this.draw.editable()

        this.map.on("baselayerchange", function (layer: any) {
            var i = 0;
            for (const [key, value] of Object.entries(LMap.baseMaps)) {
                if (key == layer.name) localStorage.setItem("map.baseMap", i.toString());
                i++;
            }
        });
        this.map.on("zoomend", (e: any) => localStorage.setItem("map.zoom", e.target._zoom));
        this.map.on("move", (e: any) => localStorage.setItem("map.center", JSON.stringify([map.getCenter().lat, map.getCenter().lng])));
    }

    clear() {
        var map = this.map;
        for (var [key, value] of Object.entries(this.overlayLayers)) {
            // console.log(this.overlayLayers[key])
            // console.log(this.overlayLayers)
            this.overlayLayers[key].eachLayer(function (layer: any) {
                map.removeLayer(layer);
            });
        }
    }

    deviceLocation() {
        if (navigator.geolocation) {
            var map = this.map;
            navigator.geolocation.getCurrentPosition(function (position) {
                map.flyTo([position.coords.latitude, position.coords.longitude], 15);
            });
        } else {
            console.log("Geolocation is not supported by this browser.");
        }
    }

    // CUSTOM OBJECTS -------------------------------------------------------------------------------

    // addDevice($latlng: any = [0, 0], draggable = false) {
    //     var map = this.map;
    //     let marker = L.marker($latlng, {
    //         icon: this.icons.device(),
    //         draggable: draggable,
    //     }) as any;

    //     marker.on("click", function (e: any) {
    //         map.setView(e.latlng, 17);
    //     });

    //     this.trucks.push(marker);
    //     this.addToOverlayLayer("Device", [marker]);

    //     return marker;
    // }

    addTruck($latlng = [0, 0] as any, draggable = false, onClick = null as any) {
        var map = this.map;
        var marker = L.marker($latlng, {
            icon: this.icons.truck(),
            draggable: draggable,
        }).on("click", function (e) {
            if (onClick == null) {
                map.setView(e.latlng, 17);
            } else {
                onClick();
            }
        });

        this.trucks.push(marker);
        this.addToOverlayLayer("Truck", [marker]);

        return marker;
    }

    addDeliveryPoint($latlng = [0, 0] as any, $config = { draggable: false } as any, radius = 100) {
        var map = this.map;
        Object.assign($config, {
            color: "#ffc107",
            type: "deliveryMarker",
            icon: this.icons.deliveryPoint(),
        });

        var marker = L.marker($latlng, $config);

        var geofence = L.circle($latlng, {
            radius: radius,
        });

        var extremeGeofence = L.circle($latlng, {
            radius: radius + radius / 2,
            color: "#ffffff",
        });

        var id = $config.id;
        this.deliveryPoints.push({ id, marker, geofence, extremeGeofence });

        this.addToOverlayLayer("Delivery Points", [marker]);
        this.addToOverlayLayer("Geofence", [geofence, extremeGeofence]);

        return { id, marker, geofence, extremeGeofence };
    }

    addWarehouse(warehouse: any) {
        var map = this.map;

        // console.log($geojson);

        var polygon = L.geoJSON(warehouse.area, {
            color: "#3388FF",
            weight: 2,
        } as any);

        console.log(polygon.getBounds());

        var marker = L.marker([polygon.getBounds().getCenter().lat, polygon.getBounds().getCenter().lng], {
            color: "#3388FF",
            icon: L.divIcon({
                html: '<i class="fa fa-warehouse fa-2x text-success warehouse-marker" style="text-shadow:0 0 5px rgba(0,0,0,.15)"></i>',
                iconSize: [20, 20],
                className: "marker-icon",
            }),
        } as any);

        if (warehouse?.name != null && warehouse?.address != null)
            marker.bindPopup(`
            <div class="">
                <p class="h5"><i class="fa fa-warehouse"></i> ${warehouse.name}</p>
                <p class="small m-0"><i class="fa fa-map-marked"></i> ${warehouse.address}</p>
            </div>
        `);
        // polygon.editing.intialize();
        // polygon.editing.enable();
        // for (var [key, value] of Object.entries(polygon._layers)) {
        //     // console.log(`${key}: ${value}`);
        //     console.log(value.editing.enable());
        //   }
        // console.log();
        // polygon._layers.each(el => {
        //     console.log(el);
        // })

        this.addToOverlayLayer("Warehouses", [polygon, marker]);

        return { polygon, marker };
    }

    // addRoute($polyline: any, $config: any) {
    //     var map = this.map;
    //     var polyline = L.polyline($polyline, $config);
    //     this.addToOverlayLayer("Route", [polyline]);
    // }

    setApiRoute($polyline: any) {
        // console.log($polyline.coordinates);
        this.apiRoute.clearLayers();
        this.apiRoute = L.geoJSON($polyline);
        this.apiRoute.setStyle({
            stroke: true,
            weight: 5,
            color: "#22252d",
            opacity: 0.5,
            lineCap: "square",
            lineJoin: "bevel",
            bubblingMouseEvents: true,
        });
        this.apiRoute.bindTooltip("Estimated Route");
        // this.apiRoute.setStyle({ weight: "5", opacity: '0.75', stroke: '50' })
        // this.apiRoute.bindPopup('Given Route')
        this.addToOverlayLayer("Route", [this.apiRoute]);
    }

    fitOverviewBound() {
        var group = L.featureGroup([this.overlayLayers["Delivery Points"], this.overlayLayers["Warehouses"]]);

        this.map.fitBounds(group.getBounds());
    }
    // setTrackingRoute($polyline: any) {
    //     // console.log($polyline.coordinates);
    //     this.trackingRoute.clearLayers();
    //     this.trackingRoute = L.geoJSON($polyline);
    //     this.trackingRoute.setStyle({
    //         stroke: true,
    //         weight: 5,
    //         color: "#2bb14a",
    //         opacity: 0.75,
    //         lineCap: "square",
    //         lineJoin: "bevel",
    //         bubblingMouseEvents: true,
    //     });
    //     this.overlayLayers["Route"].removeLayer();

    //     this.addToOverlayLayer("Tracking Route", [this.trackingRoute]);
    // }

    // HELPERS ----------------------------------------------------------------------

    addToOverlayLayer(layer: any, objects: any = [], $config = { pan: true }) {
        var map = this.map;
        if (!this.overlayLayers[layer]) {
            this.overlayLayers[layer] = L.featureGroup().addTo(this.map);

            if ($config.pan) {
                // this.overlayLayers[layer].on("click", function (e) {
                //     map.flyTo(e.latlng, 14)
                // })

                this.overlayLayers[layer].on("dblclick", function (e: any) {
                    map.setView(e.latlng, 17);
                });
            }

            this.layerControl.addOverlay(this.overlayLayers[layer], layer);
        }
        objects.forEach((el: any) => {
            this.overlayLayers[layer].addLayer(el);
        });

        return this.overlayLayers[layer];
    }

    draw = {
        controls: () => {
            var drawControl = new L.Control.Draw();
            this.map.addControl(drawControl);
        },
        createPolygon: () => {
            var polygonDrawer = new L.Draw.Polygon(this.map);
            // Click handler for you button to start drawing polygons
            polygonDrawer.enable();

            return polygonDrawer;
        },
        actions: () => {
            var map = this.map;
            var LMap = this;
            // map.on(L.Draw.Event.CREATED, function (e) {
            //     var type = e.layerType,
            //         layer = e.layer;
            //     if (type === 'marker') {
            //         // Do marker specific actions
            //     }
            //     console.log('created')
            //     // Do whatever else you need to. (save to db; add to map etc)
            //     layer.editing.enable();

            //     LMap.addToOverlayLayer('Editable', [layer], { pan: false })
            //     // .on('click', function (e) {
            //     // layer.editing.enable();
            //     // })

            //     // this.draw.editable();
            // });
            // map.on('draw:created draw:deleted draw:deletestart draw:deletestop draw:drawstart draw:drawstop draw:drawvertex draw:edited draw:editmove draw:editresize draw:editstart draw:editstop draw:editvertex draw:markercontext draw:toolbarclosed draw:toolbaropened', function (e) {
            // map.on("draw:editvertex", function (e) {
            //     var layers = e.layers;
            //     console.log(e);
            //     // layers.eachLayer(function (layer) {
            //     //     console.log(layer);
            //     //     //do whatever you want; most likely save back to db
            //     // });
            // });
        },
        editable: (layer = "Editable") => {
            // if (this.overlayLayers['Warehouses'])
            this.addToOverlayLayer(layer);
            this.overlayLayers[layer].on("click", function (e: any) {
                e.layer.editing.enable();
            });
        },
        editableLayer: (layer: any) => {
            for (var [key, value] of Object.entries(layer._layers)) {
                // console.log(`${key}: ${value}`);
                (value as any).editing.enable();
            }

            // if (this.overlayLayers['Warehouses'])
        },
    };

    // set map(map) {
    //     this.map = map
    // }
    // get map() {
    //     return this.map
    // }
}

