import { Link, NavLink, Outlet, useLocation } from "react-router-dom";

export default function LayoutHome() {
    let location = useLocation();

    return (
        <main className="text-dark">
            <div className="backdrop" style={{ position: "relative", zIndex: 0 }}>
                <div className="d-flex justify-content-between align-items-start flex-wrap">
                    <div className="p-3 text-light">
                        <p className="h3">Delivery Logistics Efficiency Enabler</p>
                        <p style={{ maxWidth: 430 }}>Lowering the cost of logistics operation &amp; Hawk eye visibility of deliveries is the key to Happier Customer Deliveries</p>
                    </div>

                    <div className="mb-3 ms-auto">
                        <div className="px-3 mt-3" style={{ background: "linear-gradient(0deg,#707176,#d9dadf)", borderRadius: "1rem 0 0 1rem" }}>
                            <nav className="navbar navbar-expand navbar-dark p-0">
                                <ul className="nav navbar-nav" style={{ padding: ".35rem .5rem" }}>
                                    <li className="nav-item">
                                        <NavLink to={"/"} className="nav-link ">
                                            <i className="fa fa-home"></i> Home
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink to={"/pricing"} className="nav-link ">
                                            <i className="fas fa-rupee-sign"></i> Pricing
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink to={"/about"} className="nav-link ">
                                            <i className="fa fa-address-card"></i> About Us
                                        </NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink to={"/contact"} className="nav-link ">
                                            <i className="fa fa-phone-alt"></i> Contact Us
                                        </NavLink>
                                    </li>
                                    <li className="nav-item dropdown ">
                                        <a href="#" className="nav-link" data-bs-toggle="dropdown">
                                            <i className="fa fa-bars"></i>
                                        </a>
                                        <div className="dropdown-menu dropdown-menu-end dropdown-menu-dark">
                                            <a className="dropdown-item small" href="#">
                                                Terms &amp; Condition
                                            </a>
                                            <a className="dropdown-item small" href="#">
                                                Pivacy Policy
                                            </a>
                                        </div>
                                    </li>
                                </ul>
                            </nav>
                        </div>

                        <div className="mt-3 text-end px-3">
                            <Link to={"/pricing"} className="btn btn-sm btn-outline-secondary text-light" style={{ background: "#0b1312" }}>
                                <i className="fa fa-shopping-cart"></i> Order Now
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
            <Outlet />
        </main>
    );
}
